import React from 'react';
import { NavLink } from 'react-router-dom';
import Page from '../layout/page';

import ParceiroList from './parceiroList';
import ParceiroForm from './parceiroForm';
import ParceiroFormEdit from './parceiroFormEdit';
import ParceiroSingle from './parceiroSingle';
import ParceiroSingleProcedimentos from './parceiroSingleProcedimentos';
import ParceiroSingleAgendamentos from './parceiroSingleAgendamentos';
import ParceiroProcedimentoSingle from './parceiroProcedimentoSingle';
import ParceiroProcedimentoFormEdit from './parceiroProcedimentoFormEdit';


const icon = 'user-md';
const title = 'Parceiros';

export const ParceiroIndex = (props) => (
	<Page title={title} icon={icon}>
		<ParceiroList />
	</Page>
);

export const ParceiroAdd = (props) => (
	<Page title={title} icon={icon}>
		<ParceiroForm />
	</Page>
);

export const ParceiroEdit = ({props, match}) => (
	<Page title={title} icon={icon}>
		<ParceiroFormEdit match={match} />
	</Page>
);

export const ParceiroProcedimentoView = ({props, match}) => (
	<Page title={title} icon={icon}>
		<ParceiroProcedimentoSingle match={match} />
	</Page>
);

export const ParceiroProcedimentoEdit = ({props, match}) => (
	<Page title={title} icon={icon}>
		<ParceiroProcedimentoFormEdit match={match} />
	</Page>
);

export class ParceiroView extends React.Component {

	constructor({props, match}) {
	  super(props);
	
	  this.state = { match };
	}

	componentWillReceiveProps(nextProps) {
		const match = { ...this.state.match };
		if (nextProps.match.params.action !== this.props.match.params.action) {
			match.params.action = nextProps.match.params.action;
			this.setState({ match });
		}
	}

	render() {
		return (
			<Page title={title} icon={icon}>
				<ul className="nav nav-tabs">
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/parceiro/v/${this.state.match.params.id}`}>
							Detalhes
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/parceiro/v/${this.state.match.params.id}/procedimentos`}>
							Procedimentos
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/parceiro/v/${this.state.match.params.id}/agendamentos`}>
							Agendamentos
						</NavLink>
					</li>
				</ul>
				<br/>
				{
					this.state.match.params.action === 'procedimentos' ?
					<ParceiroSingleProcedimentos match={this.state.match} />
					: this.state.match.params.action === 'agendamentos' ?
					<ParceiroSingleAgendamentos match={this.state.match} />
					:
					<ParceiroSingle match={this.state.match} />
				}
			</Page>
		);
	}
}
