import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import { Link } from 'react-router-dom';
import Load from '../layout/load';

class RepresentanteSinglePagamentos extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			pagamentos: [],
			searchOptions: [
				{name: 'Data', value: 'data_criacao'},
				{name: 'Valor', value: 'valor_representante'}
            ],
			status: false,
			message: 'Carregando...',
			isLoaded: false
        };
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`representante/${this.props.match.params.id}/pagamento`)
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status === false) {
						this.setState(response.data);
					} else if (response.data) {
						this.setState({pagamentos: response.data, status: true});
					} else {
						this.setState({message: 'Nenhum pagamento encontrado'});
					}
				});
		}
    }

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<table className="table">
					<thead>
						<tr>
							<th>Código</th>
							<th>Data criação</th>
							<th>Valor</th>
						</tr>
					</thead>
					<tbody>
						{
							this.state.status ?
								this.state.pagamentos.map(pagamento => 
									<tr key={`reppag${pagamento.id_pagamento}`}>
										<td><Link to={`/representante/v/${this.props.match.params.id}/pagamentos/${pagamento.id_representante_pagamento}`}>{pagamento.id_representante_pagamento.padStart(10, '0')}</Link></td>
										<td>{pagamento.data_criacao.replace(/^(\d{4})-(\d{2})-(\d{2}) (\d)/, '$3/$2/$1 $4')}</td>
										<td>R$ {pagamento.total_pagamento || 0}</td>
									</tr>
								)
							:
								<tr>
									<td align="center" colSpan="5">{this.state.message}</td>
								</tr>
						}
					</tbody>
				</table>
			</div>
		)
	}
}

export default RepresentanteSinglePagamentos;