import React from 'react';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import FileSaver from 'file-saver';

const reportUrl = 'relatorio/agendamento';

class RelatorioAgendamento extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
            agendamentos: [],
			searchOptions: [
				{name: 'Parceiro', value: 'nome'},
			],
			searchFilters: [
				{name: 'Data inicial', value: 'data_inicial', props: {type: 'date'}},
				{name: 'Data final', value: 'data_final', props: {type: 'date'}},
			],
			search: {
                value: '',
                field: '',
                filters: ''
            },
			isLoaded: false
		};

		this.filter = this.filter.bind(this);
		this.handleDownload = this.handleDownload.bind(this);
	}

	componentDidMount() {	
        axiosAuth.get(`${reportUrl}?${window.location.href.replace(/^.+\?/, '')}`)
            .then(response => {
                this.setState({ isLoaded: true });
                if (response.data.status !== false) {
                    this.setState({ agendamentos: response.data });
                }
            });
	}

	handleDownload() {
        const { value, field, filters } = this.state.search;
        const url = value ? `${reportUrl}/${value}/${field}?${filters}&export=csv` : `${reportUrl}?${filters}&export=csv`; 
        axiosAuth.get(url)
			.then((response) => {
            	var blob = new Blob([response.data.replaceAll('.', ',')], {type: 'text/csv;charset=utf-8'});
            	FileSaver.saveAs(blob, "relatorio.csv");
        	});
    }

	filter(value, field, filters) {
		const url = value ? `${reportUrl}/${value}/${field}?${filters}` : `${reportUrl}?${filters}`; 
		this.setState({ isLoaded: false, search: { value, field, filters } });
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.setState({ agendamentos: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				{/* <Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} /> */}
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
                                <th>Número</th>
                                <th>Parceiro</th>
								<th>Valor Parceiro</th>
								<th>Valor CF</th>
                                <th>Valor Total</th>
                                <th>Status</th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.agendamentos.map(agendamento => (
									<tr>
                                        <td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
                                        <td><Link to={`/parceiro/v/${agendamento.id_parceiro}`}>{agendamento.nome}</Link></td>
                                        <td>R$ {agendamento.valor_parceiro}</td>
                                        <td>R$ {agendamento.valor_cf}</td>
                                        <td>R$ {agendamento.total}</td>
                                        <td>{agendamento.status}</td>
                                    </tr>
								))
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={this.handleDownload} title='Download' icon='download' />
			</div>
		);
	}
}

export default RelatorioAgendamento;