import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
// import html2canvas from 'html2canvas';
import PFSingle from '../pessoaFisica/pfSingle';
import EnderecoSingle from '../endereco/enderecoSingle';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class ClienteSingle extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			foto: '',
			status: '',
			pf: {},
			endereco: {},
			isLoaded: false
		};
	}

	componentWillMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`cliente/${this.props.match.params.id}`)
				.then(response => {
					this.setState({ isLoaded: true, ...response.data });
				});
		}
	}

	// print() {
	// 	var config = {
	// 		backgroundColor: null
	// 	}
	// 	html2canvas(document.querySelector('.carteira'), config).then(canvas => {
	// 		var image = new Image();
    //     	image.src = canvas.toDataURL();
			
	// 		var w = window.open("", 'Carteira');
    //     	w.document.write(image.outerHTML);
	// 	});
	// }

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<div className="carteira">
					<div className="carteira-frente">
						{/* <button className="btn btn-secondary btn-imprimir" data-html2canvas-ignore title="Imprimir" onClick={this.print}><FontAwesomeIcon icon="print" /></button> */}
						<p className="carteira-logo">Consulta Fácil</p>
						<small>localhost:4500</small>
						<div className="carteira-dados">
							<h5>Número</h5>
							<p>{this.state.pf.cpf}</p>
							<h5>Nome</h5>
							<p>{this.state.pf.nome}</p>
						</div>
					</div>
					{/* <div className="carteira-fundo">
						<p className="carteira-slogan">A saúde que cabe no seu bolso!</p>
					</div> */}
				</div>
				<div className="d-print-none">
					<dl className="row">
						<dt className="col-sm-3">Matrícula</dt>
						<dd className="col-sm-9">{this.state.pf.cpf && this.state.pf.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</dd>
					</dl>
					<PFSingle pf={this.state.pf} />
					<EnderecoSingle endereco={this.state.endereco} />
					<FloatButton onClick={false} icon="pencil-alt" title="Editar" to={`/cliente/e/${this.props.match.params.id}`} />
				</div>
			</div>
		);
	}
}

export default ClienteSingle;