import axiosAuth from '../../helpers/axiosAuth';

const URL = 'cliente';

export const getCliente = (id = '', cb = function(){}) => {
	const clientePromise = axiosAuth.get(`${URL}/${id}`); 

	return dispatch => {
		clientePromise.then(response => {
			dispatch({ type: 'CLIENTE_GET', payload: response.data });
			cb();
		});
	}
}

export const addCliente = (pf, endereco) => {
	const clientePromise = axiosAuth.post(URL, { pf, endereco });

	return dispatch => {
		clientePromise.then(response => dispatch({ type: 'CLIENTE_ADD', payload: response.data }));
	}
}