import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { FormText, Button, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import Select from 'react-select';
import axiosAuth from '../../helpers/axiosAuth';
import { handleChange, handleSelectChange, clearForm } from '../form/formActions';
import { clearState } from '../../ducks/pages';
import Load from '../layout/load';

class ProcedimentoForm extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			codigos: [],
			tipos: [
				{label: 'Consulta', value: 'consulta'},
				{label: 'Exame', value: 'exame'},
				{label: 'Procedimento', value: 'procedimento'}
			],
			especialidades: [],
			disabled: false,
			isLoaded: false
		}

		this.addProcedimento = this.addProcedimento.bind(this);
		this.handleCodigoChange = this.handleCodigoChange.bind(this);
		this.updateNome = this.updateNome.bind(this);
	}

	componentDidMount() {
		axiosAuth.get('procedimento/especialidade').then(response => {
			this.setState({especialidades: response.data, isLoaded: true});
		});
	}

	handleCodigoChange(value) {
		if (value.length > 1) {
			axiosAuth.get(`procedimento/tuss/${value}`).then(response => {
				this.setState({codigos: response.data});
			});
		} else {
			this.setState({codigos: []});
		}
	}

	addProcedimento(event) {
		event.preventDefault();
		if (this.state.disabled) {
			return false;
		}
		this.setState({disabled: true});
		
		const procedimento = {
			nome: this.props.nome,
			tipo: this.props.tipo.value,
			codigo_tuss: this.props.codigo_tuss.value,
			codigo_especialidade: this.props.codigo_especialidade ? this.props.codigo_especialidade.value : null
		};
		
		axiosAuth.post('procedimento', procedimento)
			.then(response => {
				if (response.status === false) {
					alert(response.data.message);
				} else {
					this.props.clear('PROCEDIMENTO');
					this.props.clearState('parceirosById');
					alert('Procedimento salvo com sucesso!');
					window.location.href = '/#/procedimento';
				}
				this.setState({disabled: false});;
			})
			.catch(err => {
				alert('Ocorreu um erro ao cadastrar o procedimento. Por favor, tente novamente!');
				this.setState({disabled: false});;
			});
	}

	updateNome(value) {
		if (value.search(' - ') > -1) {
			this.props.change('nome', value.split(/ - (.+)/)[1]);
		}
	}

	render() {
		return (
			<form onSubmit={this.addProcedimento}>
				{this.state.isLoaded ? '': <Load />}
				<Row>
					<Col sm="4">
						<FormGroup>
							<Label for="tipo">Tipo</Label>
							<Select options={this.state.tipos} value={this.props.tipo} onChange={(selectedOption) => this.props.changeSelect('tipo', selectedOption)} placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" required disabled={this.state.disabled} />
						</FormGroup>
					</Col>
					<Col>
						<FormGroup>
							<Label for="codigo_especialidade">Especialidade</Label>
							<Select options={this.state.especialidades} value={this.props.codigo_especialidade} onChange={(selectedOption) => this.props.changeSelect('codigo_especialidade', selectedOption)} placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" disabled={this.state.disabled} />
						</FormGroup>
					</Col>
				</Row>
				<FormGroup>
					<Label for="codigo_tuss">Código TUSS</Label>
					<Select options={this.state.codigos} value={this.props.codigo_tuss} onChange={(selectedOption) => { this.props.changeSelect('codigo_tuss', selectedOption); this.updateNome(selectedOption.label) }} onInputChange={this.handleCodigoChange} type="text" name="procedimento" id="procedimento" placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" required disabled={this.state.disabled} />
				</FormGroup>
				<FormGroup>
					<Label for="nome">Nome</Label>
					<Input type="text" name="nome" id="nome" value={this.props.nome} onChange={this.props.change} required disabled={this.state.disabled} />
					<FormText>Nome como aparecerá no sistema</FormText>
				</FormGroup>
				<Button type="submit" outline color="primary" disabled={this.state.disabled}>Salvar</Button>
			</form>
		);
	}
}

const mapStateToProps = (state) => ({
	tipo: state.procedimentoForm.tipo,
	codigo_tuss: state.procedimentoForm.codigo_tuss,
	codigo_especialidade: state.procedimentoForm.codigo_especialidade,
	nome: state.procedimentoForm.nome
})

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		change: handleChange('PROCEDIMENTO'),
		changeSelect: handleSelectChange('PROCEDIMENTO'),
		clear: clearForm,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ProcedimentoForm);