import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class ClienteList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			searchOptions: [
				{name: 'CPF/Matrícula', value: 'cpf', mask: '999.999.999-99'},
				{name: 'Nome', value: 'nome'}
			],
			isLoaded: true
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (!this.props.clientesById.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get('cliente')
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status !== false) {
						this.props.changeState({ clientesById: response.data });
					}
				});
		}
	}

	filter(value, field) {
		const url = value ? `cliente/${value}/${field}` : 'cliente';
		this.setState({ isLoaded: false });
		this.props.clearState('clientesById');
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.props.changeState({ clientesById: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Nome</th>
								<th>CPF/Matrícula</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.clientesById.map(cliente => 
									<tr key={`cliente${cliente.id_cliente}`}>
										<td><Link to={`/cliente/v/${cliente.id_cliente}`}>{cliente.nome}</Link></td>
										<td>{cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={false} to="/cliente/adicionar" />
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	clientesById: state.pages.clientesById
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		changeState,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ClienteList);