import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { Table } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import TableSearch from '../layout/tableSearch';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class ProcedimentoList extends TableSearch {

    constructor(props) {
        super(props);

        this.state = {
            columns: [
                {title: 'Código TUSS', dataIndex: 'codigo_tuss', key: 'codigo_tuss', ...this.getColumnSearchProps('codigo_tuss'),},
                {title: 'Nome', dataIndex: 'nome', key: 'nome', ...this.getColumnSearchProps('nome'), render: (nome, row) => (<Link to={`/procedimento/e/${row.id_procedimento}`}>{nome}</Link>)},
                {title: 'Tipo', dataIndex: 'tipo', key: 'tipo', render: (tipo) => (tipo ? tipo : '')},
                {title: 'Especialidade', dataIndex: 'especialidade', key: 'especialidade', ...this.getColumnSearchProps('especialidade'), render: (especialidade) => (especialidade ? especialidade : '')}
            ],
            isLoaded: true
        };
    }

    componentDidMount() {
        if (!this.props.procedimentos.length) {
            this.setState({ isLoaded: false });
            axiosAuth.get('procedimento')
                .then(response => {
                    this.setState({ isLoaded: true });
                    if (response.data.status !== false) {
                        this.props.changeState({ procedimentos: response.data });
                    }
                })
                .catch(err => {
                    alert('Ocorreu um erro ao carregar os dados');
                    this.setState({ isLoaded: true });
                });
        }
    }

    render() {
        return (
            <>
                {this.state.isLoaded ? '': <Load />}
                <div className="table-responsive">
                    <Table rowKey="id_procedimento" columns={this.state.columns} dataSource={this.props.procedimentos} />
                </div>
                <FloatButton onClick={false} to="/procedimento/adicionar" />
            </>
        );
    }
}

const mapStateToProps = (state) => ({
    procedimentos: state.pages.procedimentos
});

const mapDispatchToProps = (dispatch) => (
    bindActionCreators({
        changeState,
        clearState
    }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ProcedimentoList);