import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class ParceiroProcedimentoSingle extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            nome: '',
            valor_total: '',
            valor_parceiro: '',
            comissao: '',
            horario_atendimento: '',
            observacao: '',
            isLoaded: false
        };
    }

    componentDidMount() {
        if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`parceiro/${this.props.match.params.id_parceiro}/procedimento/${this.props.match.params.id}`)
				.then(response => {
					this.setState({ isLoaded: true, ...response.data[0] });
                })
                .catch(err => {
                    this.setState({ isLoaded: true });
                    alert('A página não pode ser carregada...');
                });
		}
    }

    render() {
        return (
            <div>
                {this.state.isLoaded ? '' : <Load />}
				<dl className="row">
					<dt className="col-sm-3">Nome</dt>
					<dd className="col-sm-9">{this.state.nome}</dd>
		
					<dt className="col-sm-3">Horário de atendimento</dt>
					<dd className="col-sm-9">{this.state.horario_atendimento}</dd>

                    <dt className="col-sm-3">Observação</dt>
					<dd className="col-sm-9">{this.state.observacao}</dd>
		
                    <dt className="col-sm-3">Valor Total <small>(Pago pelo cliente)</small></dt>
					<dd className="col-sm-9">R$ {this.state.valor_total}</dd>

					<dt className="col-sm-3">Valor Parceiro</dt>
					<dd className="col-sm-9">R$ {this.state.valor_parceiro}</dd>
		
					<dt className="col-sm-3">Valor da Comissão</dt>
					<dd className="col-sm-9">R$ {this.state.comissao}</dd>

                    <dt className="col-sm-3">Situação</dt>
					<dd className="col-sm-9">{['inativo', 'ativo'][this.state.status]}</dd>
				</dl>
                <FloatButton onClick={false} icon="pencil-alt" title="Editar" to={`/parceiro/e/${this.props.match.params.id_parceiro}/procedimentos/${this.props.match.params.id}`} />
            </div>
        );
    }
}

export default ParceiroProcedimentoSingle;