import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import { Link } from 'react-router-dom';
import Load from '../layout/load';
import FloatButton from '../layout/floatButton';

class RepresentanteSingleAgendamentos extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			agendamentos: [],
			searchOptions: [
				{name: 'Nome', value: 'nome'},
				{name: 'Código', value: 'codigo', mask: '99999999'}
            ],
            selectedAgendamentos: [],
			status: false,
			message: 'Carregando...',
			isLoaded: false
        };
        
        this.toggleSelect = this.toggleSelect.bind(this);
        this.pagar = this.pagar.bind(this);
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`representante/${this.props.match.params.id}/agendamento`)
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status === false) {
						this.setState(response.data);
					} else {
						this.setState({agendamentos: response.data, status: true});
					}
				});
		}
    }
    
    toggleSelect(id_agendamento) {
        let { selectedAgendamentos } = this.state;
        let index = selectedAgendamentos.indexOf(id_agendamento);
        if (index >= 0) {
            selectedAgendamentos.splice(index, 1);
            this.setState({selectedAgendamentos: selectedAgendamentos});
        } else {
            this.setState({selectedAgendamentos: selectedAgendamentos.concat(id_agendamento)});
        }
    }

    pagar() {
        axiosAuth.put(`representante/pagar`, {id_representante: this.props.match.params.id, agendamentos: this.state.selectedAgendamentos})
            .then(response => {
                this.setState({ isLoaded: true });
                if (response.data.status === false) {
                    alert(response.data.message);
                } else {
                    window.location.reload();
                }
            });
    }

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<table className="table">
					<thead>
						<tr>
							<th width="40"></th>
							<th>Código</th>
							<th>Parceiro</th>
							<th>Data</th>
							<th>Status</th>
							<th className="text-center">Representante pago</th>
						</tr>
					</thead>
					<tbody>
						{
							this.state.status ?
								this.state.agendamentos.map(agendamento => 
									<tr key={`cliage${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
                                        <td>{agendamento.representante_pago === '0' && <input type="checkbox" onChange={() => this.toggleSelect(agendamento.id_agendamento)} />}</td>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/parceiro/v/${agendamento.parceiro.id_parceiro}`}>{agendamento.parceiro.nome}</Link></td>
										<td>{agendamento.data_criacao.replace(/^(\d{4})-(\d{2})-(\d{2}) (\d)/, '$3/$2/$1 $4')}</td>
										<td>{agendamento.status}</td>
										<td className="text-center">{agendamento.representante_pago === '1'? 'SIM':''}</td>
									</tr>
								)
							:
								<tr>
									<td align="center" colSpan="5">{this.state.message}</td>
								</tr>
						}
					</tbody>
				</table>
                {this.state.selectedAgendamentos.length > 0 && <FloatButton title="Pagar" icon="file-invoice-dollar" onClick={this.pagar} />}
			</div>
		)
	}
}

export default RepresentanteSingleAgendamentos;