import axiosAuth from '../../helpers/axiosAuth';

export const getParceiroProcedimento = (id_parceiro = '', id_parceiro_procedimento, cb = function(){}) => {
	const parceiroProcedimentoPromise = axiosAuth.get(`parceiro/${id_parceiro}/procedimento/${id_parceiro_procedimento}`);

	return dispatch => {
		parceiroProcedimentoPromise.then(response => {
			if (response.data.status === false) {
				dispatch({ type: 'PARCEIRO_PROCEDIMENTO_GET', payload: [] });
			} else {
				dispatch({ type: 'PARCEIRO_PROCEDIMENTO_GET', payload: response.data[0] });
			}
			cb();
		});
	};
}

export const getParceiro = (id = '', cb = function(){}) => {
	const parceiroPromise = axiosAuth.get(`parceiro/${id}`);

	return dispatch => {
		parceiroPromise.then(response => {
			dispatch({ type: 'PARCEIRO_GET', payload: response.data });
			cb();
		});
	};
}

export const getParceiroPF = (cpf = '') => {
	const pfPromise = axiosAuth.get(`pessoa-fisica/${cpf.replace(/\.|-/g, '')}`); 

	return dispatch => {
		pfPromise.then(response => dispatch({ type: 'PARCEIRO_PF_GET', payload: response.data }))
			.catch(err => toggleCPF);
	}
}

export const getParceiroPJ = (cnpj = '') => {
	const pjPromise = axiosAuth.get(`pessoa-juridica/${cnpj.replace(/\.|-|\//g, '')}`); 

	return dispatch => {
		pjPromise.then(response => dispatch({ type: 'PARCEIRO_PJ_GET', payload: response.data }))
			.catch(err => toggleCNPJ);
	}
}

export const clearParceiroPF = () => {
	return { type: 'PARCEIRO_PF_CLEAR' };
}

export const clearParceiroPJ = () => {
	return { type: 'PARCEIRO_PJ_CLEAR' };
}

export const toggleCPF = () => {
	return { type: 'PARCEIRO_TOGGLE_CPF' };
}

export const toggleCNPJ = () => {
	return { type: 'PARCEIRO_TOGGLE_CNPJ' };
}