import React from 'react';
import { FormGroup, Label, Input, Button, Row, Col } from 'reactstrap';
import axiosAuth from '../../helpers/axiosAuth';
import { formatDecimal } from '../../helpers/util';

class CaixaTransacao extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            tipo: 'entrada',
            descricao: '',
            valor_pago_especie: 0,
            valor_pago_cartao: 0,
            valor_adicional_cartao: 0,
            valor_troco: 0,
            disabled: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleTipoChange = this.handleTipoChange.bind(this);
        this.handleDecimalChange = this.handleDecimalChange.bind(this);
        this.addTransacao = this.addTransacao.bind(this);
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    handleTipoChange(event) {
        if (event.target.value === 'saida') {
            this.setState({
                tipo: event.target.value,
                valor_troco: 0,
                valor_adicional_cartao: 0
            });
        } else {
            this.setState({tipo: event.target.value});
        }
    }

    handleDecimalChange(event) {
        this.setState({[event.target.name]: formatDecimal(event.target.value)});
    }

    addTransacao(event) {
        event.preventDefault();
        if (this.state.disabled) {
			return false;
		}
        this.setState({disabled: true});

        const transacao = {
            tipo: this.state.tipo,
            descricao: this.state.descricao,
            valor_pago_especie: this.state.valor_pago_especie,
            valor_pago_cartao: this.state.valor_pago_cartao,
            valor_adicional_cartao: this.state.valor_adicional_cartao,
            valor_troco: this.state.valor_troco
        };

        axiosAuth.post('caixa/transacao', transacao)
            .then(response => {
                if (response.data.status === false) {
					alert(response.data.message);
				} else {
                    alert('Transação cadastrada com sucesso!');
                    window.location.href = `/#/caixa`;
				}
            })
            .catch(err => {
                alert('Ocorreu um erro ao processar sua requisição! Por favor tente novamente.')
				this.setState({disabled: false});
            });
    }

    render() {
        return (
            <form onSubmit={this.addTransacao}>
                <Row>
                    <Col sm="4" md="3">
                        <FormGroup>
                            <Label for="tipo">Tipo</Label>
                            <Input type="select" name="tipo" id="tipo" value={this.state.tipo} required onChange={this.handleTipoChange} disabled={this.state.disabled}>
                                <option value="entrada">Entrada</option>
                                <option value="saida">Saída</option>
                            </Input>
                        </FormGroup>
                    </Col>
                    <Col>
                        <FormGroup>
                            <Label for="descricao">Descrição</Label>
                            <Input type="text" name="descricao" id="descricao" value={this.state.descricao} onChange={this.handleChange} disabled={this.state.disabled} />
                        </FormGroup>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <FormGroup>
                            <Label for="valor_pago_especie">Valor pago em espécie</Label>
                            <Input type="text" name="valor_pago_especie" id="valor_pago_especie" value={this.state.valor_pago_especie} onChange={this.handleDecimalChange} disabled={this.state.disabled} />
                        </FormGroup>
                    </Col>
                    <Col>
                        <FormGroup>
                            <Label for="valor_pago_cartao">Valor pago no cartão</Label>
                            <Input type="text" name="valor_pago_cartao" id="valor_pago_cartao" value={this.state.valor_pago_cartao} onChange={this.handleDecimalChange} disabled={this.state.disabled} />
                        </FormGroup>
                    </Col>
                    <Col>
                        <FormGroup>
                            <Label for="valor_adicional_cartao">Valor adicional do cartão</Label>
                            <Input type="text" name="valor_adicional_cartao" id="valor_adicional_cartao" value={this.state.valor_adicional_cartao} onChange={this.handleDecimalChange} disabled={this.state.disabled || this.state.tipo === 'saida'} />
                        </FormGroup>
                    </Col>
                    <Col>
                        <FormGroup>
                            <Label for="valor_troco">Troco</Label>
                            <Input type="text" name="valor_troco" id="valor_troco" value={this.state.valor_troco} onChange={this.handleDecimalChange} disabled={this.state.disabled || this.state.tipo === 'saida'} />
                        </FormGroup>
                    </Col>
                </Row>
                <Button type="submit" outline color="primary">Salvar</Button>
            </form>
        );
    }
}

export default CaixaTransacao;