import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import Load from '../layout/load';
import { alertErro } from '../layout/modalAlertActions';

class AgendamentoSingleMovimentos extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			movimentos: [],
			isLoaded: false,
			message: 'Carregando...',
			status: false
		}
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`agendamento/${this.props.match.params.id}/movimentos`)
				.then(response => {
					this.setState({isLoaded: true});
					if (response.data.status !== false) {
						this.setState({movimentos: response.data, status: true});
					} else {
						this.setState(response.data);
					}
				}).catch(err => {
					this.props.alertErro(err.response);
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<table className="table">
					<thead>
						<tr>
							<th>Data</th>
							<th>Descrição</th>
							<th>Usuário</th>
						</tr>
					</thead>
					<tbody>
						{
							this.state.status ?
								this.state.movimentos.map(movimento => 
									<tr key={`mov${movimento.id_movimento}`}>
										<td>{movimento.data_criacao.replace(/(\w{4})-(\w{2})-(\w{2}) (\w)/, '$3/$2/$1 $4')}</td>
										<td>{movimento.descricao}</td>
										<td><Link to={`/usuario/v/${movimento.created_by}`}>{movimento.email}</Link></td>
									</tr>
								)
							:
							<tr>
								<td align="center" colSpan="3">{this.state.message}</td>
							</tr>
						}
					</tbody>
				</table>	
			</div>
		);
	}
}

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro
	}, dispatch)
);

export default connect(null, mapDispatchToProps)(AgendamentoSingleMovimentos);