import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class FinanceiroList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			searchOptions: [
				{name: 'Parceiro CPF', value: 'cpf', mask: '999.999.999-99'},
				{name: 'Parceiro CNPJ', value: 'cnpj', mask: '99.999.999/9999-99'}
			],
			searchFilters: [
				{name: 'Data de cadastro', value: 'data_criacao', props: {type: 'date'}},
				{name: 'Status', value: 'status', type: 'select', options: [{value: 'pendente', label: 'Pendente'}, {value: 'realizado', label: 'Realizado'}, {value: 'cancelado', label: 'Cancelado'}]}
			],
			isLoaded: true
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (!this.props.financeiroById.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get('financeiro')
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status !== false) {
						this.props.changeState({ financeiroById: response.data });
					}
				});
		}
	}

	filter(value, field, filters) {
		const url = value ? `financeiro/${value}/${field}?${filters}` : `financeiro?${filters}`; 
		this.setState({ isLoaded: false });
		this.props.clearState('financeiroById');
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.props.changeState({ financeiroById: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Código</th>
								<th>Parceiro</th>
								<th>Data de cadastro</th>
								<th>Status</th>
								<th>Valor</th>
								<th>Ações</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.financeiroById.map(financeiro => (
									<FinanceiroListRow key={`fin${financeiro.id_financeiro}`} financeiro={financeiro} />
								))
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={false} to="/financeiro/adicionar" />
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	financeiroById: state.pages.financeiroById
});

const mapDispatchToProps = (disaptch) => (
	bindActionCreators({
		changeState,
		clearState
	}, disaptch)
); 

export default connect(mapStateToProps, mapDispatchToProps)(FinanceiroList);

class FinanceiroListRow extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			isOpen: false
		};

		this.pagar = this.pagar.bind(this);
		this.cancelar = this.cancelar.bind(this);
		this.toggle = this.toggle.bind(this);
	}

	pagar() {
		const confirma = window.confirm('Tem certeza que deseja CONFIRMAR O PAGAMENTO?');
		if (confirma) {
			axiosAuth.put(`financeiro/pagar`, {id_financeiro: this.props.financeiro.id_financeiro})
				.then(response => {
					if (response.data.status !== false) {
						window.location.reload();
					} else {
						alert('Ocorreu um erro ao confirmar o pagamento! Por favor tente novamente.');
					}
				})
				.catch(err => {
					alert('Ocorreu um erro ao processar a requisição! Por favor, tente novamente.');
				});
		}
	}

	cancelar() {
		const confirma = window.confirm('Tem certeza que deseja CANCELAR O AGAMENTAMENTO?');
		if (confirma) {
			axiosAuth.put(`financeiro/cancelar`, {id_financeiro: this.props.financeiro.id_financeiro})
				.then(response => {
					if (response.data.status !== false) {
						window.location.reload();
					} else {
						alert('Ocorreu um erro ao tentar cancelar o agendamento! Por favor tente novamente.');
					}
				})
				.catch(err => {
					alert('Ocorreu um erro ao processar a requisição! Por favor, tente novamente.');
				});
		}
	}
	
	toggle() {
		this.setState({ isOpen: !this.state.isOpen });
	}

	render () {
		const { financeiro } = this.props;

		return (
			<tr className={`status-${financeiro.status}`}>
				<td><Link to={`/financeiro/v/${financeiro.id_financeiro}`}>{financeiro.codigo}</Link></td>
				<td><Link to={`/parceiro/v/${financeiro.id_parceiro}`}>{financeiro.nome_parceiro}</Link></td>
				<td>{financeiro.data_criacao && financeiro.data_criacao.replace(/(\w{4})-(\w{2})-(\w{2}) (\w)/, '$3/$2/$1 $4')}</td>
				<td>{financeiro.status}</td>
				<td>{`R$ ${financeiro.total_parceiro}`}</td>
				<td>
					{ 
						financeiro.status === 'pendente' ? 
						<Dropdown size="sm" direction="down" isOpen={this.state.isOpen} toggle={this.toggle}>
							<DropdownToggle color="light">
								<FontAwesomeIcon icon="ellipsis-h" />
							</DropdownToggle>
							<DropdownMenu>
								<DropdownItem onClick={this.pagar}>Pagar</DropdownItem>
								<DropdownItem onClick={this.cancelar}>Cancelar</DropdownItem>
							</DropdownMenu>
						</Dropdown>
						: ''
					}
				</td>
			</tr>
		);
	}
}