import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { addProcedimento, removeProcedimento, getCliente, clearCliente, filtraParceiroProcedimento, getParceiroProcedimento, changeDesconto } from './agendamentoFormActions';
import { getProcedimentosAgendamento, getRepresentantesOptions } from '../../ducks/app';
import { handleChange, handleSelectChange, clearForm, handleDecimalChange } from '../form/formActions';
import { alertErro } from '../layout/modalAlertActions';
import { clearState } from '../../ducks/pages';

import { Button, FormGroup, Label, Input, Row, Col, FormText } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import InputMask from 'react-input-mask';
import Select from 'react-select'
import Load from '../layout/load';
import FormGroupLoading from '../form/formGroupLoading';

class AgendamentoForm extends React.Component {


	constructor(props) {
		super(props);

		this.addAgendamento = this.addAgendamento.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.somaProcedimentos = this.somaProcedimentos.bind(this);
		this.somaTotal = this.somaTotal.bind(this);
		this.calculaTroco = this.calculaTroco.bind(this);
		this.calculaAdicional = this.calculaAdicional.bind(this);
		this.changeTipoCartao = this.changeTipoCartao.bind(this);
		this.valorAjustado = this.valorAjustado.bind(this);
		this.handleCpfChange = this.handleCpfChange.bind(this);

		this.state = {
			// procedimentosByCodigo: [],
			// procedimentosFiltrados: [],
			procedimento: '',
			parceirosFiltrados: [],
			disabled: props.disabled || false,
			disabledCPF: this.props.match.params.cpf || false,
			searchingCPF: false,
			isLoaded: true
		};
	}
	
	componentDidMount() {
		this.props.clear('AGENDAMENTO');
	
		if (this.props.parceiroProcedimentoById.length === 0) {
			this.props.getParceiroProcedimento();
		}
		if (this.props.procedimentosAgendamentoOptions.length === 0) {
			this.props.getProcedimentosAgendamento();
		}
		if (this.props.representantesOptions.length === 0) {
			this.props.getRepresentantesOptions();
		}
		if (this.props.match.params.cpf) {
			this.handleCpfChange({target:{name:'cliente_cpf', value: this.props.match.params.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}});
		}
	}

	handleChange(event) {
		const { target } = event;
		this.setState({[target.name]: target.value});
	}

	handleCpfChange(event) {
		const cpf = event.target.value;
		
		this.props.change(event);
		
		if (cpf.length === 14) {
			this.setState({searchingCPF: true});
			this.props.getCliente(cpf, () => this.setState({searchingCPF: false}));
		} else {
			this.props.clearCliente();
		}
	}

	somaProcedimentos() {
		const total = this.props.procedimentos.reduce( (soma, procedimento) => {
			soma += (this.props.parceiroProcedimentoById[procedimento.id_parceiro_procedimento].valor_total * procedimento.quantidade) - procedimento.desconto;
			return soma;
		}, 0 );

		return total;
	}

	somaTotal() {
		return this.somaProcedimentos() + Number(this.props.valor_adicional_cartao) + Number(this.props.valor_representante);
	}

	calculaTroco() {
		const troco = (Number(this.props.valor_pago_especie) + Number(this.props.valor_pago_cartao)) - this.somaProcedimentos() - Number(this.props.valor_representante);
		return troco.toFixed(2);
	}

	calculaAdicional(event) {
		const taxa = this.props.tipo_cartao === 'credito'? 0.1 : 0.05;
		this.props.changeDecimal(event, null, (value) => {
			this.props.change('valor_adicional_cartao', Number(value * taxa).toFixed(2));
		});
	}

	changeTipoCartao(event) {
		const taxa = event.target.value === 'credito'? 0.1 : 0.05;
		this.props.change(event);
		this.props.change('valor_adicional_cartao', Number(this.props.valor_pago_cartao * taxa).toFixed(2));
	}

	valorAjustado(){
		return Number(Number(this.props.valor_pago_cartao) + Number(this.props.valor_adicional_cartao)).toFixed(2);
	}

	addAgendamento(event) {
		event.preventDefault();
		const troco = this.calculaTroco();

		if (this.state.disabled || !this.state.isLoaded) {
			return false;
		}

		if (troco < 0) {
			alert("Valor de pagamento (espécie + cartão) menor que valor devido!");
			return false;
		} else if (troco > Number(this.props.valor_pago_especie)) {
			alert("Valor do troco maior que valor pago em espécie!");
			return false;
		}

		const agendamento = {
			procedimentos: this.props.procedimentos,
			valor_pago_especie: this.props.valor_pago_especie,
			valor_pago_cartao: this.props.valor_pago_cartao,
			valor_adicional_cartao: this.props.valor_adicional_cartao,
			valor_representante: this.props.valor_representante,
			valor_troco: troco,
			id_cliente: this.props.id_cliente,
			id_representante: this.props.representante.value
		};

		this.setState({disabled: true});

		axiosAuth.post('agendamento', agendamento)
			.then(response => {
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					this.props.clear('AGENDAMENTO');
					this.props.clearState('agendamentosById');
					alert('Agendamento cadastrado com sucesso!');
					response.data.map(id_agendamento => {
						window.open(`/#/agendamento/v/${id_agendamento}`, '_blank');
					});
				}
				this.setState({disabled: false});
			})
			.catch(err => {
				if (err.response && err.response.data) {
					this.props.alertErro(err.response.data.erros);
				} else {
					alert('Ocorreu um erro ao tentar salvar o agendamento! Pro favor, tente novamente.');
				}
				this.setState({disabled: false});
			});
	}

	render() {
		return (
			<form onSubmit={this.addAgendamento}>
				{this.state.isLoaded || <Load />}
				<fieldset>
					{/*<input type="hidden" id="id_cliente" value={this.props.id_cliente} />*/}
					<legend>Cliente</legend>
					<Row>
						<Col sm="4" md="3">
							<FormGroupLoading loading={this.state.searchingCPF}>
								<Label for="cliente_cpf">CPF</Label>
								<InputMask mask="999.999.999-99" maskChar="" name="cliente_cpf" id="cliente_cpf" className="form-control" value={this.props.cliente_cpf} onChange={this.props.match.params.cpf? null : this.handleCpfChange} required disabled={this.state.disabledCPF || this.state.searchingCPF} />
							</FormGroupLoading>
						</Col>
						<Col>
							<FormGroup>
								<Label for="cliente_nome">Nome</Label>
								<Input type="text" name="cliente_nome" id="cliente_nome" value={this.props.cliente_nome} required disabled />
							</FormGroup>
						</Col>
					</Row>
				</fieldset>
				<fieldset>
					{/*<input type="hidden" id="id_parceiro_procedimento" value={this.props.procedimentos} />*/}
					<legend>Procedimento</legend>
					<Row>
						<Col>
							<FormGroup>
								<Label for="procedimento">Nome do procedimento</Label>
								<Select options={this.props.procedimentosAgendamentoOptions} value={this.props.procedimento} onChange={(selectedOption) => { this.props.changeSelect('procedimento', selectedOption); this.props.filtraParceiroProcedimento(selectedOption) }} type="text" name="procedimento" id="procedimento" placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" />
							</FormGroup>
						</Col>
						<Col sm="2">
							<FormGroup>
								<Label for="procedimento_quantidade">Qnt.</Label>
								<Input type="number" name="procedimento_quantidade" id="procedimento_quantidade" min="1" value={this.props.procedimento_quantidade} onChange={this.props.change} />
							</FormGroup>
						</Col>
					</Row>
					<FormGroup className="form-group-icon">
						<Label for="procedimento_codigo">Parceiro / Valor</Label>
						<Select options={this.props.procedimentos_filtrados} id="procedimento_codigo" name="procedimento_codigo" value={this.props.procedimento_codigo} onChange={(selectedOption) => this.props.changeSelect('procedimento_codigo', selectedOption)} placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" disabled={this.props.disabledProcedimentos} />
						<button type="button" onClick={this.props.addProcedimento} className="btn btn-primary" disabled={this.props.disabledProcedimentos}><FontAwesomeIcon icon="plus" /></button>
					</FormGroup>
				</fieldset>
				<fieldset>
					<legend>Resumo do agendamento</legend>
					<table className="table">
						<thead>
							<tr>
								<th>Procedimento</th>
								<th>Parceiro</th>
								<th width="90">Qnt.</th>
								<th width="130">Valor Unit.</th>
								<th width="130">Desconto</th>
							</tr>
						</thead>
						<tfoot>
							{
								this.props.valor_representante > 0 && <tr className="table-secondary">
									<td colSpan="3"></td>
									<td><b>Representante</b></td>
									<td><b>{`R$ ${this.props.valor_representante || 0}`}</b></td>
								</tr>
							}
							{
								this.props.valor_adicional_cartao > 0 && <tr className="table-secondary">
									<td colSpan="3"></td>
									<td><b>Ajuste cartão</b></td>
									<td><b>{`R$ ${this.props.valor_adicional_cartao || 0}`}</b></td>
								</tr>
							}
							<tr className="table-primary">
								<td colSpan="3"></td>
								<td><b>TOTAL</b></td>
								<td><b>{`R$ ${this.somaTotal()}`}</b></td>
								<td></td>
							</tr>
						</tfoot>
						<tbody>
							{
								this.props.procedimentos.map((procedimento, i) => (
									<tr key={`agendamentoProcedimento${i}`}>
										<td>{procedimento.id_procedimento.label}</td>
										<td>{this.props.parceiroProcedimentoById[procedimento.id_parceiro_procedimento].parceiro}</td>
										<td>{procedimento.quantidade}</td>
										<td>{`R$ ${this.props.parceiroProcedimentoById[procedimento.id_parceiro_procedimento].valor_total}`}</td>
										<td><Input name={`desconto_${i}`} type="number" value={procedimento.desconto} min={0} max={(this.props.parceiroProcedimentoById[procedimento.id_parceiro_procedimento].valor_total-this.props.parceiroProcedimentoById[procedimento.id_parceiro_procedimento].valor_parceiro)*1} step="0.1" onChange={this.props.changeDesconto} /></td>
										<td><button type="button" onClick={() => this.props.removeProcedimento(procedimento.id_parceiro_procedimento)} className="btn btn-danger" title="Remover"><FontAwesomeIcon icon="times" /></button></td>
									</tr>
								))
							}
						</tbody>
					</table>
				</fieldset>
				<fieldset>
					<FormGroup>
						<legend>Representante</legend>
						<Row>
							<Col>
								<FormGroup>
									<Label for="representante">Nome do representante</Label>
									<Select options={this.props.representantesOptions} value={this.props.representante} onChange={(selectedOption) => { this.props.changeSelect('representante', selectedOption); }} type="text" name="representante" id="representante" placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" />
								</FormGroup>
							</Col>
							<Col sm="3">
								<FormGroup>
									<Label for="valor_representante">Valor representante</Label>
									<Input type="text" name="valor_representante" id="valor_representante" value={this.props.valor_representante} onChange={this.props.changeDecimal} disabled={!this.props.representante} />
								</FormGroup>
							</Col>
						</Row>
					</FormGroup>
				</fieldset>
				<fieldset>
					<legend>Dados do pagamento</legend>
					<Row>
						<Col>
							<FormGroup>
								<Label for="valor_pago_especie">Valor em espécie</Label>
								<Input type="text" name="valor_pago_especie" id="valor_pago_especie" value={this.props.valor_pago_especie} onChange={this.props.changeDecimal} />
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="valor_pago_cartao">Valor cartão</Label>
								<Input type="text" name="valor_pago_cartao" id="valor_pago_cartao" value={this.props.valor_pago_cartao} onChange={this.calculaAdicional} disabled={!this.props.tipo_cartao} />
							</FormGroup>
							<Row>
								<Col>
									<FormGroup check>
										<Label>
											<Input type="radio" name="tipo_cartao" value="credito" onChange={this.changeTipoCartao} /> Crédito
										</Label>
									</FormGroup>
								</Col>
								<Col>
									<FormGroup check>
										<Label>
											<Input type="radio" name="tipo_cartao" value="debito" onChange={this.changeTipoCartao} /> Débito
										</Label>
									</FormGroup>
								</Col>
							</Row>
						</Col>
						<Col>
							<FormGroup>
								<Label for="valor_adicional_cartao">Valor ajustado</Label>
								<Input type="text" id="valor_adicional_cartao" value={this.valorAjustado()} readOnly />
								<FormText color="muted">Valor cobrado na maquineta</FormText>
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label>Troco</Label>
								<Input type="text" value={this.calculaTroco()} readOnly />
							</FormGroup>
						</Col>
					</Row>
				</fieldset>
				<Button type="submit" outline color="primary">Salvar</Button>
			</form>
		);
	}
}

const mapStateToProps = (state) => ({
	representantesOptions: state.app.representantesOptions,
	procedimentosAgendamentoOptions: state.app.procedimentosAgendamentoOptions,
	cliente_nome: state.agendamentoForm.cliente_nome,
	cliente_cpf: state.agendamentoForm.cliente_cpf,
	parceiroProcedimentoById: state.agendamentoForm.parceiroProcedimentoById,
	procedimento: state.agendamentoForm.procedimento,
	procedimento_codigo: state.agendamentoForm.procedimento_codigo,
	procedimento_quantidade: state.agendamentoForm.procedimento_quantidade,
	procedimentos_filtrados: state.agendamentoForm.procedimentos_filtrados,
	representante: state.agendamentoForm.representante,
	id_cliente: state.agendamentoForm.id_cliente,
	procedimentos: state.agendamentoForm.procedimentos,
	valor_pago_cartao: state.agendamentoForm.valor_pago_cartao,
	valor_adicional_cartao: state.agendamentoForm.valor_adicional_cartao,
	tipo_cartao: state.agendamentoForm.tipo_cartao,
	valor_representante: state.agendamentoForm.valor_representante,
	valor_pago_especie: state.agendamentoForm.valor_pago_especie,
	disabledProcedimentos: state.agendamentoForm.disabledProcedimentos
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		getProcedimentosAgendamento,
		getParceiroProcedimento,
		filtraParceiroProcedimento,
		addProcedimento,
		removeProcedimento,
		getCliente,
		getRepresentantesOptions,
		clearCliente,
		changeDesconto,
		change: handleChange('AGENDAMENTO'),
		changeDecimal: handleDecimalChange('AGENDAMENTO'),
		changeSelect: handleSelectChange('AGENDAMENTO'),
		clear: clearForm,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(AgendamentoForm);