import axiosAuth from '../../helpers/axiosAuth';

export const getProcedimento = (id = '', cb = function(){}) => {
    const procedimentoPromise = axiosAuth.get(`procedimento/?where=id_procedimento%3D${id}`);

    return dispatch => {
        procedimentoPromise.then(response => {
            dispatch({ type: 'PROCEDIMENTO_GET', payload: response.data[0] });
            cb();
        });
    };
}