import React from 'react';
import { NavLink } from 'react-router-dom';
import Page from '../layout/page';

import RepresentanteList from './representanteList';
import RepresentanteForm from './representanteForm';
import RepresentanteSingle from './representanteSingle';
import RepresentanteSingleAgendamentos from './representanteSingleAgendamentos';
import RepresentanteSinglePagamentos from './representanteSinglePagamentos';
import RepresentantePagamentosSingle from './representantePagamentosSingle';

const icon = 'handshake';
const title = 'Representante';

export const RepresentanteIndex = (props) => (
	<Page title={title} icon={icon}>
		<RepresentanteList />
	</Page>
);

export const RepresentanteAdd = (props) => (
	<Page title={title} icon={icon}>
		<RepresentanteForm />
	</Page>
);

export class RepresentanteView extends React.Component {

	constructor({props, match}) {
	  super(props);
	
	  this.state = { match };
	}

	componentWillReceiveProps(nextProps) {
		const match = { ...this.state.match };
		if (nextProps.match.params.action !== this.props.match.params.action) {
			match.params.action = nextProps.match.params.action;
			this.setState({ match });
		}
	}

	render() {
		return (
			<Page title={title} icon={icon}>
				<ul className="nav nav-tabs">
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/representante/v/${this.state.match.params.id}`}>
							Detalhes
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/representante/v/${this.state.match.params.id}/agendamentos`}>
							Agendamentos
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/representante/v/${this.state.match.params.id}/pagamentos`}>
							Pagamentos
						</NavLink>
					</li>
				</ul>
				<br/>
				{
				 	this.state.match.params.action === 'agendamentos' ?
					<RepresentanteSingleAgendamentos match={this.state.match} />
					:
					this.state.match.params.action === 'pagamentos' ?
					<RepresentanteSinglePagamentos match={this.state.match} />
					:
					<RepresentanteSingle match={this.state.match} />
				}
			</Page>
		);
	}
}

export const RepresentantePagamentoView = ({props, match}) => (
	<Page title={title} icon={icon}>
		<RepresentantePagamentosSingle match={match} />
	</Page>
);