import React from 'react';
import { NavLink } from 'react-router-dom';
import Page from '../layout/page';

import AgendamentoList from './agendamentoList';
import AgendamentoForm from './agendamentoForm';
import AgendamentoSingle from './agendamentoSingle';
import AgendamentoSingleMovimentos from './agendamentoSingleMovimentos';

const icon = 'file';
const title = 'Agendamentos';

export const AgendamentoIndex = (props) => (
	<Page title={title} icon={icon}>
		<AgendamentoList />
	</Page>
);

export const AgendamentoAdd = ({props, match}) => (
	<Page title={title} icon={icon}>
		<AgendamentoForm match={match} />
	</Page>
);

export class AgendamentoView extends React.Component {

	constructor({props, match}) {
		super(props);

		this.state = { match };
	}

	render() {
	 	return (
			<Page title={title} icon={icon} className="page-80mm">
				<ul className="nav nav-tabs">
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/agendamento/v/${this.state.match.params.id}`}>
							Detalhes
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/agendamento/v/${this.state.match.params.id}/movimentos`}>
							Movimentos
						</NavLink>
					</li>
				</ul>
				<br/>
				{
					this.state.match.params.action === 'movimentos' ?
					<AgendamentoSingleMovimentos match={this.state.match} />
					:
					<AgendamentoSingle match={this.state.match} />
				}
			</Page>
		)
	}
}