import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import axiosAuth from '../../helpers/axiosAuth';
import { alertErro } from '../layout/modalAlertActions';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class AgendamentoList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			searchOptions: [
				{name: 'Número', value: 'numero'},
				{name: 'Cliente CPF', value: 'cpf', mask: '999.999.999-99'}
			],
			searchFilters: [
				{name: 'Data de cadastro', value: 'data_criacao', props: {type: 'date'}},
				{name: 'Status', value: 'status', type: 'select', options: [{value: 'pendente', label: 'Pendente'}, {value: 'aguardando pagamento', label: 'Aguardando pagamento'}, {value: 'realizado', label: 'Realizado'}, {value: 'cancelado', label: 'Cancelado'}]}
			],
			isLoaded: true,
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (!this.props.agendamentosById.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get('agendamento')
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status !== false) {
						this.props.changeState({ agendamentosById: response.data });
					}
				});
		}
	}

	filter(value, field, filters) {
		const url = value ? `agendamento/${value}/${field}?${filters}` : `agendamento?${filters}`; 
		this.setState({ isLoaded: false });
		this.props.clearState('agendamentosById');
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.props.changeState({ agendamentosById: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Número</th>
								<th>Parceiro</th>
								<th>Cliente CPF</th>
								<th className="text-center">Total procedimentos</th>
								<th>Status</th>
								<th>Ações</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.agendamentosById.map(agendamento => 
									<tr key={`agendamento${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/parceiro/v/${agendamento.parceiro.id_parceiro}`}>{agendamento.parceiro.nome}</Link></td>
										<td><Link to={`/cliente/v/${agendamento.cliente.id_cliente}`}>{agendamento.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</Link></td>
										<td className="text-center">{`R$ ${agendamento.total}`}</td>
										<td>{agendamento.status}</td>
										<td><AgendamentoRowActions agendamento={agendamento} handleErro={this.props.alertErro} /></td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={false} to="/agendamento/adicionar" />
			</div>
		);
	}
}

class AgendamentoRowActions extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			isOpen: false
		};

		this.cancelar = this.cancelar.bind(this);
		this.toggle = this.toggle.bind(this);
	}

	cancelar() {
		const confirma = window.confirm('Tem certeza que deseja CANCELAR O AGAMENTAMENTO?');
		if (confirma) {
			axiosAuth.put(`agendamento/cancelar`, {id_agendamento: this.props.agendamento.id_agendamento})
				.then(response => {
					if (response.data.status === false) {
						this.props.handleErro(response.data.message);
					} else {
						window.location.reload();
					}
				})
				.catch(err => {
					if (err.response.data && err.response.data.erros) {
						this.props.handleErro(err.response.data.erros);
					} else {
						alert('Ocorreu um erro ao processar a requisição! Por favor, tente novamente.');
					}
				});
		}
	}
	
	toggle() {
		this.setState({ isOpen: !this.state.isOpen });
	}

	render() {
		const { agendamento } = this.props;

		return (
			['pendente', 'realizado'].includes(agendamento.status) ? 
			<Dropdown size="sm" direction="down" isOpen={this.state.isOpen} toggle={this.toggle}>
				<DropdownToggle color="light">
					<FontAwesomeIcon icon="ellipsis-h" />
				</DropdownToggle>
				<DropdownMenu>
					<DropdownItem onClick={this.cancelar}>Cancelar</DropdownItem>
				</DropdownMenu>
			</Dropdown>
			: ''
		);
	}
}

const mapStateToProps = (state) => ({
	agendamentosById: state.pages.agendamentosById
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		changeState,
		clearState
	}, dispatch)
)

export default connect(mapStateToProps, mapDispatchToProps)(AgendamentoList);