import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { getProcedimentos } from '../../ducks/app';
import { handleChange, handleSelectChange, clearForm } from '../form/formActions';

import { FormText, FormGroup, Label, Input, InputGroup, InputGroupAddon, Row, Col } from 'reactstrap';
import Select from 'react-select';

class ParceiroProcedimentoForm extends React.Component {

	constructor(props) {
		super(props);

		this.addProcedimento = this.addProcedimento.bind(this);
	}
	
	componentDidMount() {
		this.props.clear('PARCEIRO_PROCEDIMENTO');
		this.props.change({target: document.querySelector('#id_parceiro')}, this.props.parceiro);
		if (this.props.procedimentosById.length === 0) {
			this.props.getProcedimentos();
		}
	}

	addProcedimento(event) {
		event.preventDefault();

		const parceiroProcedimento = {
			observacao: this.props.observacao,
			comissao: this.props.comissao,
			valor_parceiro: this.props.valor_parceiro,
			horario_atendimento: this.props.horario_atendimento,
			id_parceiro: this.props.id_parceiro,
			id_procedimento: this.props.id_procedimento.value,
		};
		axiosAuth.post('parceiroprocedimento', parceiroProcedimento)
			.then(response => {
				if (response.data.status === false) {
					alert(response.message);
				} else {
					this.props.clear('PARCEIRO_PROCEDIMENTO');
					alert('PROCEDIMENTO CADASTRADO COM SUCESSO!');
				}
			});
	}

	render() {
		return (
			<form>
				<fieldset>
					<legend>Dados Procedimento</legend>
					<input type="hidden" name="id_procedimento" id="id_procedimento" value={this.props.id_procedimento} required />
					<input type="hidden" name="id_parceiro" id="id_parceiro" value={this.props.id_parceiro} required />
					<FormGroup>
						<Label for="nome">Nome</Label>
						<Select options={this.props.procedimentosOptions} id="nome" required value={this.props.id_procedimento} onChange={(selectedOption) => this.props.changeSelect('id_procedimento', selectedOption)} placeholder="Selecione..." noResultsText="Nenhum resultado encontrado" />
					</FormGroup>
					<Row>
						<Col>
							<FormGroup>
								<Label for="valor_parceiro">Valor Parceiro</Label>
								<InputGroup>
									<InputGroupAddon addonType="prepend">R$</InputGroupAddon>
									<Input type="text" name="valor_parceiro" id="valor_parceiro" required value={this.props.valor_parceiro} onChange={this.props.change} />
								</InputGroup>
								<FormText>Valor em reais e com centavos separado por ponto.</FormText>
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="comissao">Valor da Comissão</Label>
								<InputGroup>
									<InputGroupAddon addonType="prepend">R$</InputGroupAddon>
									<Input type="text" name="comissao" id="comissao" required value={this.props.comissao} onChange={this.props.change} />
								</InputGroup>
								<FormText>Valor em reais e com centavos separado por ponto.</FormText>
							</FormGroup>
						</Col>
					</Row>
					<FormGroup>
						<Label for="horario_atendimento">Horário de Atendimento</Label>
						<Input type="text" name="horario_atendimento" id="horario_atendimento" required value={this.props.horario_atendimento} onChange={this.props.change} />
					</FormGroup>
					<FormGroup>
						<Label for="observacao">Observação</Label>
						<Input type="text" name="observacao" id="observacao" value={this.props.observacao} onChange={this.props.change} />
					</FormGroup>
				</fieldset>
				<button type="submit" className="btn btn-outline-primary" onClick={this.addProcedimento}>Adicionar</button>
			</form>
		);
	}
};

const mapStateToProps = (state) => ({
	procedimentosById: state.app.procedimentosById,
	procedimentosOptions: state.app.procedimentosOptions,
	codigo: state.parceiroProcedimentoForm.codigo,
	nome: state.parceiroProcedimentoForm.nome,
	observacao: state.parceiroProcedimentoForm.observacao,
	comissao: state.parceiroProcedimentoForm.comissao,
	valor_parceiro: state.parceiroProcedimentoForm.valor_parceiro,
	horario_atendimento: state.parceiroProcedimentoForm.horario_atendimento,
	id_procedimento: state.parceiroProcedimentoForm.id_procedimento,
	id_parceiro: state.parceiroProcedimentoForm.id_parceiro
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		getProcedimentos,
		change: handleChange('PARCEIRO_PROCEDIMENTO'),
		changeSelect: handleSelectChange('PARCEIRO_PROCEDIMENTO'),
		clear: clearForm
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ParceiroProcedimentoForm);

export const pfFormSchema = {
	'nome': null,
	'nome_social': null,
	'data_nascimento': null,
	'cpf': null,
	'rg': null,
	'sexo': 1,
	'email': null,
	'telefone': null,
	'telefone2': null,
	'pai': null,
	'mae': null,
	'responsavel': null
};