import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import QRCode from 'qrcode.react';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

import { Button } from 'reactstrap';
import { Popconfirm } from 'antd';

class AgendamentoSingle extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			codigo: '',
			total: '',
			valor_representante: '',
			valor_adicional_cartao: '',
			data_criacao: '',
			cliente: {},
			parceiro: {
				endereco: {}
			},
			procedimentos: [],
			isLoaded: false
		};

		this.print = this.print.bind(this);
		this.somaTotal = this.somaTotal.bind(this);
		this.deleteProcedimento = this.deleteProcedimento.bind(this);
		this.cancelar = this.cancelar.bind(this);
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {

			axiosAuth.get(`agendamento/${this.props.match.params.id}`)
				.then(response => {
					this.setState({ isLoaded: true, ...response.data[0] });
				});
		}
	}

	print() {
		axiosAuth.post('http://cfacilimp:3003', this.state)
			.then(response => {
				if (response.data === true) {
					alert('Agendamento impresso com sucesso!');
				}
			})
			.catch(err => {
				alert('Impressora não encontrada.');
			});
	}

	somaTotal() {
		return Number(Number(this.state.total) + Number(this.state.valor_representante) + Number(this.state.valor_adicional_cartao)).toFixed(2);
	}

	deleteProcedimento(id_parceiro_procedimento) {
		axiosAuth.delete(`agendamento/${this.props.match.params.id}/procedimento/${id_parceiro_procedimento}`)
			.then(response => {
				if (response.status === false) {
					alert(response.data.message);
				} else {
                    alert('Procedimento removido com sucesso!');
                    window.location.reload();
				}
			})
			.catch(err => {
				alert('Ocorreu um erro ao processar a requisição. Por favor, tente novamente!');
				this.setState({disabled: false});
			});
	}

	cancelar() {
		axiosAuth.put(`agendamento/cancelar`, {id_agendamento: this.props.match.params.id})
			.then(response => {
				if (response.data.status === false) {
					this.props.handleErro(response.data.message);
				} else {
					window.location.reload();
				}
			})
			.catch(err => {
				if (err.response.data && err.response.data.erros) {
					this.props.handleErro(err.response.data.erros);
				} else {
					alert('Ocorreu um erro ao processar a requisição! Por favor, tente novamente.');
				}
			});
	}

	render() {
		const { endereco } = this.state.parceiro;
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<div className="row">
					<div className="col-sm-9">
						<dl className="row">
							<dt className="col-sm-3">Código</dt>
							<dd className="col-sm-9">{this.state.codigo}</dd>
							<dt className="col-sm-3">Data de cadastro</dt>
							<dd className="col-sm-9">{this.state.data_criacao ? new Date(this.state.data_criacao).toLocaleDateString('pt-BR') : ''}</dd>
							<dt className="col-sm-3">Parceiro</dt>
							<dd className="col-sm-9">{this.state.parceiro.nome}</dd>
							<dt className="col-sm-3">Funcionamento</dt>
							<dd className="col-sm-9">{this.state.parceiro.horario_funcionamento}</dd>
							<dt className="col-sm-3">Telefone</dt>
							<dd className="col-sm-9">{this.state.parceiro.telefone}</dd>
							<dt className="col-sm-3">Endereço</dt>
							<dd className="col-sm-9">
								{endereco.logradouro ? `${endereco.logradouro}, ${endereco.numero} - ${endereco.bairro}` : ''}<br/>
								{endereco.complemento}
							</dd>
							<dt className="col-sm-3">Status</dt>
							<dd className="col-sm-9">{this.state.status}</dd>
						</dl>
						<h3>Cliente</h3>
						<dl className="row">
							<dt className="col-sm-3">Matrícula</dt>
							<dd className="col-sm-9">{this.state.cliente.cpf && this.state.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</dd>
							<dt className="col-sm-3">Nome</dt>
							<dd className="col-sm-9">{this.state.cliente.nome}</dd>
							<dt className="col-sm-3">Telefone</dt>
							<dd className="col-sm-9">{this.state.cliente.telefone}</dd>
						</dl>
					</div>
					<div className="col-sm-3">
						{
							this.state.codigo ? 
								<QRCode value={`http://localhost:4500/parceiro/#/agendamento/adicionar/${this.state.codigo}`} className="float-right" />
							: ''
						}
					</div>
				</div>
				<h3>Procedimentos</h3>
				<table className="table">
					<thead>
						<tr>
							<th>Procedimento</th>
							<th>Horário de Atendimento</th>
							<th width="90">Qnt.</th>
							<th width="130">Valor Unit.</th>
							<th width="130">Desconto</th>
						</tr>
					</thead>
					<tfoot>
						{
							this.state.valor_representante > 0 && <tr className="table-secondary">
								<td colSpan="3"></td>
								<td><b>Representante</b></td>
								<td><b>{`R$ ${this.state.valor_representante || 0}`}</b></td>
							</tr>
						}
						{
							this.state.valor_adicional_cartao > 0 && <tr className="table-secondary">
								<td colSpan="3"></td>
								<td><b>Ajuste cartão</b></td>
								<td><b>{`R$ ${this.state.valor_adicional_cartao || 0}`}</b></td>
							</tr>
						}
						<tr className="table-primary">
							<td colSpan="3"></td>
							<td><b>TOTAL</b></td>
							<td><b>{`R$ ${this.somaTotal()}`}</b></td>
						</tr>
					</tfoot>
					<tbody>
						{
							this.state.procedimentos.map(procedimento => (
								<tr key={`agendamento-procedimento-${procedimento.codigo_tuss}-${procedimento.quantidade}`}>
									<td>{procedimento.codigo_tuss} - {procedimento.nome}</td>
									<td>{procedimento.horario_atendimento}</td>
									<td>{procedimento.quantidade}</td>
									<td width="130">{`R$ ${procedimento.valor_total}`}</td>
									<td width="130">
										{`R$ ${procedimento.desconto}`} 
										{
											(['pendente', 'realizado'].includes(this.state.status)) && <Popconfirm title="Tem certeza que deseja remover este procedimento?" onConfirm={() => {return (this.state.procedimentos.length > 1)? this.deleteProcedimento(procedimento.id_parceiro_procedimento) : this.cancelar() }} okText="Sim" cancelText="Não">
												<Button type="button" size="sm" outline color="danger">Excluir</Button>
											</Popconfirm>
										}
									</td>
								</tr>
							))
						}
					</tbody>
				</table>	
				<FloatButton onClick={this.print} icon="print" title="Imprimir" />
			</div>
		);
	}
}

export default AgendamentoSingle;