import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { Table } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import TableSearch from '../layout/tableSearch';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class RepresentanteList extends TableSearch {

    constructor(props) {
        super(props);

        this.state = {
            columns: [
				{title: 'Nome', dataIndex: 'nome', key: 'nome', ...this.getColumnSearchProps('nome'), render: (nome, row) => (<Link to={`/representante/v/${row.id_representante}`}>{nome}</Link>)},
				{title: 'CPF', dataIndex: 'cpf', key: 'cpf', ...this.getColumnSearchProps('cpf')},
				{title: 'Telefones', dataIndex: 'telefones', key: 'telefones'},
			],
			isLoaded: true
		};
    }

    componentDidMount() {
        if (!this.props.representantes.length) {
            this.setState({ isLoaded: false });
            axiosAuth.get('representante')
                .then(response => {
                    this.setState({ isLoaded: true });
                    if (response.data.status !== false) {
                        this.props.changeState({ representantes: response.data });
                    }
                });
        }
    }

    render() {
        return (
            <div>
                {this.state.isLoaded || <Load />}
                <div className="table-responsive">
					<Table rowKey="id_representante" columns={this.state.columns} dataSource={this.props.representantes} />
				</div>
                <FloatButton onClick={false} to="/representante/adicionar" />
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    representantes: state.pages.representantes
});

const mapDispatchToProps = (dispatch) => (
    bindActionCreators({
        changeState,
        clearState
    }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(RepresentanteList);