import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import { Link } from 'react-router-dom';
import Search from '../layout/search';
import Load from '../layout/load';

class ParceiroSingleAgendamentos extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			agendamentos: [],
			searchOptions: [
				{name: 'Número', value: 'numero'},
				{name: 'Cliente CPF', value: 'cpf', mask: '999.999.999-99'}
			],
			searchFilters: [
				{name: 'Data de cadastro', value: 'data_criacao', props: {type: 'date'}},
				{name: 'Status', value: 'status', type: 'select', options: [{value: 'pendente', label: 'Pendente'}, {value: 'realizado', label: 'Realizado'}, {value: 'cancelado', label: 'Cancelado'}, {value: 'cancelado', label: 'Cancelado'}]}
			],
			status: false,
			message: '',
			isLoaded: false
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`parceiro/${this.props.match.params.id}/agendamento`)
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status === false) {
						this.setState(response.data);
					} else {
						this.setState({agendamentos: response.data, status: true});
						
					}
				});
		}
	}

	filter(value, field, filters) {
		const url = value ? `parceiro/${this.props.match.params.id}/agendamento/${value}/${field}?${filters}` : `parceiro/${this.props.match.params.id}/agendamento?${filters}`; 
		this.setState({ isLoaded: false, agendamentos: [] });
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.setState({ agendamentos: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Número</th>
								<th>Parceiro</th>
								<th>Cliente CPF</th>
								<th>Total</th>
								<th>Status</th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.agendamentos.map(agendamento => 
									<tr key={`agendamento${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/parceiro/v/${agendamento.parceiro.id_parceiro}`}>{agendamento.parceiro.nome}</Link></td>
										<td><Link to={`/cliente/v/${agendamento.cliente.id_cliente}`}>{agendamento.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</Link></td>
										<td>{`R$ ${agendamento.total}`}</td>
										<td>{agendamento.status}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
			</div>
		);
	}
}

export default ParceiroSingleAgendamentos;