import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import axiosAuth from '../../helpers/axiosAuth';
import { Button } from 'reactstrap';
import PFForm from '../pessoaFisica/pfForm';
import EnderecoForm from '../endereco/enderecoForm';
import { getCliente } from './clienteActions';
import { handleChange, handleUpdate, clearForm } from '../form/formActions';
import { alertErro } from '../layout/modalAlertActions';
import { clearState } from '../../ducks/pages';

class ClienteForm extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			disabled: props.disabled,
			disabledPF: false
		}

		this.addCliente = this.addCliente.bind(this);
		this.cbCPF = this.cbCPF.bind(this);
	}

	componentDidMount() {
		this.props.clear('CLIENTE');

		if (this.props.match && this.props.match.params.id) {
			this.props.getCliente(this.props.match.params.id);
		}
	}

	addCliente(event) {
		event.preventDefault();

		if (this.state.disabled) {
			return false;
		}

		const cliente = {
			pf: {...this.props.pf},
			endereco: {...this.props.endereco}
		};

		this.setState({disabled: true});

		axiosAuth.post('cliente', cliente)
			.then(response => {
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					this.props.clear('CLIENTE');
					this.props.clearState('clientesById');
					alert('Cliente adicionado com sucesso!');
				}
				this.setState({disabled: false, disabledPF: false});
			})
			.catch(err => {
				alert('Ocorreu um erro ao processar sua requisição! Por favor tente novamente.')
				this.setState({disabled: false});
			});
	};

	cbCPF(data) {
		if (data.id_cliente) {
			alert('CLIENTE JÁ CADASTRADO');
			window.location.href = `/#/cliente/v/${data.id_cliente}`;
		} else if (data.id_pessoa_fisica) {
			this.setState({disabledPF: true});
			this.props.change({target:{name: 'pf.id_pessoa_fisica'}}, data.id_pessoa_fisica);
			this.props.update(data.pf, 'pf');
		}
	}

	render() {
		return (
			<form onSubmit={this.addCliente}>
				<PFForm pf={this.props.pf} change={this.state.disabled ? undefined : this.props.change} cbCPF={this.cbCPF} disabled={this.state.disabled || this.state.disabledPF} />
				<EnderecoForm endereco={this.props.endereco} change={this.state.disabled ? undefined : this.props.change} disabled={this.state.disabled} />
				{
					this.state.disabled ? '' : (
						<Button type="submit" outline color="primary">Salvar</Button>
					)
				}
			</form>
		);
	}
}

const mapStateToProps = (state) => ({
	cpf: state.clienteForm.cpf,
	pf: state.clienteForm.pf,
	endereco: state.clienteForm.endereco
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		change: handleChange('CLIENTE'),
		update: handleUpdate('CLIENTE'),
		clear: clearForm,
		getCliente,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ClienteForm);