import React from 'react';
import { LocaleProvider } from 'antd';
import ptBR from 'antd/lib/locale-provider/pt_BR';
import Sidebar from './components/layout/sidebar';
import { Route } from 'react-router-dom';
import ModalAlert from './components/layout/modalAlert';
import { AgendamentoIndex, AgendamentoAdd, AgendamentoView } from './components/agendamento/agendamentoPages';
import { CaixaIndex, CaixaAdd, CaixaView, CaixaTransacaoAdd } from './components/caixa/caixaPages';
import { ClienteIndex, ClienteAdd, ClienteEdit, ClienteView } from './components/cliente/clientePages';
import { FinanceiroIndex, FinanceiroAdd, FinanceiroView } from './components/financeiro/financeiroPages';
import { ParceiroIndex, ParceiroAdd, ParceiroEdit, ParceiroView, ParceiroProcedimentoEdit, ParceiroProcedimentoView } from './components/parceiro/parceiroPages';
import { ProcedimentoIndex, ProcedimentoAdd, ProcedimentoEdit } from './components/procedimento/procedimentoPages';
import { RepresentanteIndex, RepresentanteAdd, RepresentanteView, RepresentantePagamentoView } from './components/representante/representantePages';
import { UsuarioIndex, UsuarioAdd, UsuarioEdit, UsuarioView, UsuarioProfile } from './components/usuario/usuarioPages';
import Home from './components/layout/home';
import { Logout } from './components/login/loginPages';
import { RelatorioAgendamentoPage, RelatorioFinanceiroPage, RelatorioParceiroPage, RelatorioProcedimentoPage } from './components/relatorio/relatorioPages';

class App extends React.Component {

	render() {
		return (
			<LocaleProvider locale={ptBR}>
				<div className="App">
					<div className="container-fluid">
						<ModalAlert />
						<div className="row flex-xl-nowrap">
							<Sidebar />
							<main className="main col-md-9 col-xl-10 ml-sm-auto">
								<Route exact path="/" component={Home} />
								<Route exact path="/agendamento" component={AgendamentoIndex} />
								<Route exact path="/agendamento/adicionar/:cpf?" component={AgendamentoAdd} />
								<Route exact path="/agendamento/v/:id" component={AgendamentoView} />
								<Route exact path="/agendamento/v/:id/:action" component={AgendamentoView} />
								<Route exact path="/caixa" component={CaixaIndex} />
								<Route exact path="/caixa/v/:id" component={CaixaView} />
								<Route exact path="/caixa/adicionar/" component={CaixaAdd} />
								<Route exact path="/caixa/transacao/" component={CaixaTransacaoAdd} />
								<Route exact path="/cliente" component={ClienteIndex} />
								<Route exact path="/cliente/v/:id" component={ClienteView} />
								<Route exact path="/cliente/e/:id" component={ClienteEdit} />
								<Route exact path="/cliente/v/:id/:action" component={ClienteView} />
								<Route exact path="/cliente/adicionar" component={ClienteAdd} />
								<Route exact path="/financeiro" component={FinanceiroIndex} />
								<Route exact path="/financeiro/v/:id" component={FinanceiroView} />
								<Route exact path="/financeiro/adicionar" component={FinanceiroAdd} />
								<Route exact path="/parceiro" component={ParceiroIndex} />
								<Route exact path="/parceiro/adicionar" component={ParceiroAdd} />
								<Route exact path="/parceiro/v/:id" component={ParceiroView} />
								<Route exact path="/parceiro/e/:id" component={ParceiroEdit} />
								<Route exact path="/parceiro/v/:id/:action" component={ParceiroView} />
								<Route exact path="/parceiro/v/:id_parceiro/procedimentos/:id" component={ParceiroProcedimentoView} />
								<Route exact path="/parceiro/e/:id_parceiro/procedimentos/:id" component={ParceiroProcedimentoEdit} />
								<Route exact path="/procedimento" component={ProcedimentoIndex} />
								<Route exact path="/procedimento/adicionar" component={ProcedimentoAdd} />
								<Route exact path="/procedimento/e/:id" component={ProcedimentoEdit} />
								<Route exact path="/relatorio/agendamento" component={RelatorioAgendamentoPage} />
								<Route exact path="/relatorio/financeiro" component={RelatorioFinanceiroPage} />
								<Route exact path="/relatorio/parceiro" component={RelatorioParceiroPage} />
								<Route exact path="/relatorio/procedimento" component={RelatorioProcedimentoPage} />
								<Route exact path="/representante" component={RepresentanteIndex} />
								<Route exact path="/representante/adicionar" component={RepresentanteAdd} />
								<Route exact path="/representante/v/:id" component={RepresentanteView} />
								<Route exact path="/representante/v/:id/:action" component={RepresentanteView} />
								<Route exact path="/representante/v/:id_representante/pagamentos/:id_representante_pagamento" component={RepresentantePagamentoView} />
								<Route exact path="/usuario" component={UsuarioIndex} />
								<Route exact path="/usuario/adicionar" component={UsuarioAdd} />
								<Route exact path="/usuario/v/:id" component={UsuarioView} />
								<Route exact path="/usuario/e/:id" component={UsuarioEdit} />
								<Route exact path="/meus-dados" component={UsuarioProfile} />
								<Route exact path="/logout" component={Logout} />
							</main>
						</div>
					</div>
				</div>
			</LocaleProvider>
		);
	}
}

export default App;
