import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { Radio } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import { handleChange, handleSelectChange, clearForm } from '../form/formActions';
import { clearState } from '../../ducks/pages';
import { alertErro } from '../layout/modalAlertActions';
import Load from '../layout/load';
import { getParceirosOptions } from '../../ducks/app';
import { filterAgendamentosByParceiro, checkAgendamento } from './financeiroFormActions';
import { FormGroup, Button, Label } from 'reactstrap';
import Select from 'react-select';


class FinanceiroForm extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			financeiro_tipo: 'ciclo',
			isLoaded: true,
			disabled: false,
			total: 0
		}

		this.addFinanceiro = this.addFinanceiro.bind(this);
		this.somaProcedimentos = this.somaProcedimentos.bind(this);
		this.changeTipoFinanceiro = this.changeTipoFinanceiro.bind(this);
		this.geraFinanceiro = this.geraFinanceiro.bind(this);
	}

	componentDidMount() {
		if (!this.props.parceirosOptions.length) {
			this.props.getParceirosOptions();
		} 
	}

	addFinanceiro(event) {
		event.preventDefault();

		if (this.state.disabled) {
			return false;
		}

		const financeiro = {
			id_parceiro: this.props.id_parceiro.value,
			agendamentos: this.props.agendamentos
		};

		this.setState({disabled: true});

		axiosAuth.post('financeiro', financeiro)
			.then(response => {
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					this.props.clear('FINANCEIRO');
					this.props.clearState('financeiroById');
					this.props.clearState('agendamentosById');
					alert('Financeiro cadastrado com sucesso!');
				}
				this.setState({total: 0, disabled: false});
			})
			.catch(err => {
				if (err.response) {
					this.props.alertErro(err.response.data.erros);
				}
				this.setState({disabled: false});
			})
	}

	toggleSelects(event) {
		document.querySelectorAll('.check-agendamento').forEach(check => {
			if (check.checked !== event.target.checked) {
				check.click();
			}
		});
	}

	somaProcedimentos() {
		let total = 0;
		document.querySelectorAll('.check-agendamento:checked').forEach(check => {
			total += parseFloat(check.parentElement.parentElement.querySelector('.agendamento-valor').innerText.replace('R$ ', ''));
		});

		this.setState({total});
	}

	changeTipoFinanceiro(event) {
		this.setState({financeiro_tipo: event.target.value});
	}

	geraFinanceiro() {
		if (!this.state.isLoaded) {
			return false;
		}
		this.setState({isLoaded: false});
		
		axiosAuth.post('financeiro/fecha_ciclos')
			.then(response => {
				this.setState({isLoaded: true});
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					alert('Financeiro(s) gerado(s) com sucesso!');
					window.location.href = `/#/financeiro`;
				}
			})
			.catch(err => {
				this.setState({isLoaded: true});
				alert('Ocorreu um erro ao processar a requisição!');
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Radio.Group onChange={this.changeTipoFinanceiro} value={this.state.financeiro_tipo} className="mb-3">
					<Radio value="ciclo">Em lote</Radio>
					<Radio value="individual">Individual</Radio>
				</Radio.Group>
				{
					this.state.financeiro_tipo === 'ciclo' ? 
						<div>
							<p>Ao clicar no botão abaixo serão gerados <b>financeiros</b> automaticamente para todos os parceiros com ciclo fechados até a presente data.</p>
							<Button outline color="primary" onClick={this.geraFinanceiro} disabled={!this.state.isLoaded}>Gerar financeiro em lote</Button> 
						</div>
					: this.state.financeiro_tipo === 'individual' ?
						<form onSubmit={this.addFinanceiro}>
							<FormGroup>
								<Label for="id_parceiro">Parceiro</Label>
								<Select options={this.props.parceirosOptions} value={this.props.id_parceiro} onChange={(selectedOption) => { this.setState({isLoaded: false}); this.props.changeSelect('id_parceiro', selectedOption); this.props.filterAgendamentosByParceiro(selectedOption, () => {this.setState({isLoaded: true})}) } } type="text" name="id_parceiro" id="id_parceiro" placeholder="Selecione..." noResultsText="Nenhum parceiro encontrado" />
							</FormGroup>
							<div className="table-responsive">
								<table className='table'>
									<thead className="">
										<tr>
											<th><input type="checkbox" onChange={this.toggleSelects} /></th>
											<th>Número</th>
											<th>Cliente CPF</th>
											<th>Total Parceiro</th>
											<th>Comissão</th>
											<th>Status</th>
										</tr>
									</thead>
									<tfoot>
										<tr className="table-primary">
											<td colSpan="4"></td>
											<td><b>TOTAL PARCEIRO</b></td>
											<td><b>{`R$ ${this.state.total}`}</b></td>
										</tr>
									</tfoot>
									<tbody>
										{
											this.props.parceiroAgendamentos.map(agendamento => 
												<tr key={`agendamento${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
													<td><input type="checkbox" className="check-agendamento" value={agendamento.id_agendamento} onChange={event => { this.props.checkAgendamento(event); this.somaProcedimentos() }} /></td>
													<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
													<td><Link to={`/cliente/v/${agendamento.cliente.id_cliente}`}>{agendamento.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</Link></td>
													<td className="agendamento-valor">{`R$ ${agendamento.total_parceiro}`}</td>
													<td>{`R$ ${agendamento.total_comissao}`}</td>
													<td>{agendamento.status}</td>
												</tr>
											)
										}
									</tbody>
								</table>
							</div>
							<Button type="submit" outline color="primary">Adicionar</Button>
						</form> 
					: ''
				}
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	parceirosOptions: state.app.parceirosOptions,
	id_parceiro: state.financeiroForm.id_parceiro,
	agendamentos: state.financeiroForm.agendamentos,
	parceiroAgendamentos: state.financeiroForm.parceiroAgendamentos,
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		change: handleChange('FINANCEIRO'),
		changeSelect: handleSelectChange('FINANCEIRO'),
		clear: clearForm,
		clearState,
		getParceirosOptions,
		filterAgendamentosByParceiro,
		checkAgendamento
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(FinanceiroForm);