import React from 'react';

class PJSingle extends React.Component {

	render() {
		return (
			<div>
				<h3>Pessoa Jurídica</h3>
				<dl className="row">
					<dt className="col-sm-3">Razão Social</dt>
					<dd className="col-sm-9">{this.props.pj.razao_social}</dd>

					<dt className="col-sm-3">Nome Fantasia</dt>
					<dd className="col-sm-9">{this.props.pj.nome_fantasia}</dd>

					<dt className="col-sm-3">CNPJ</dt>
					<dd className="col-sm-9">{this.props.pj.cnpj && this.props.pj.cnpj.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, '$1.$2.$3/$4-$5')}</dd>

					<dt className="col-sm-3">E-mail</dt>
					<dd className="col-sm-9">{this.props.pj.email}</dd>

					<dt className="col-sm-3">Telefone</dt>
					<dd className="col-sm-9">{this.props.pj.telefone}</dd>

					<dt className="col-sm-3">Telefone 2</dt>
					<dd className="col-sm-9">{this.props.pj.telefone2}</dd>

					<dt className="col-sm-3">Nome do responsável</dt>
					<dd className="col-sm-9">{this.props.pj.nome_responsavel}</dd>

					<dt className="col-sm-3">CPF do responsável</dt>
					<dd className="col-sm-9">{this.props.pj.cpf_responsavel}</dd>

					<dt className="col-sm-3">E-mail do responsável</dt>
					<dd className="col-sm-9">{this.props.pj.email_responsavel}</dd>

					<dt className="col-sm-3">Telefone do responsável</dt>
					<dd className="col-sm-9">{this.props.pj.telefone_responsavel}</dd>
				</dl>
			</div>
		);
	}
}

export default PJSingle;