import React from 'react';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class FinanceiroSingle extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			financeiro: {},
			isLoaded: false
		};
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`financeiro/${this.props.match.params.id}`)
				.then(response => {
					this.setState({ isLoaded: true, financeiro: response.data[0] });
				});
		}
	}

	render() {
		const { financeiro } = this.state;

		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<dl className="row">
					<dt className="col-sm-3">Código</dt>
					<dd className="col-sm-9">{financeiro.codigo}</dd>

					<dt className="col-sm-3">Parceiro</dt>
					<dd className="col-sm-9"><Link to={`/parceiro/v/${financeiro.id_parceiro}`}>{financeiro.nome_parceiro}</Link></dd>

					<dt className="col-sm-3">Data de cadastro</dt>
					<dd className="col-sm-9">{financeiro.data_criacao && financeiro.data_criacao.replace(/(\w{4})-(\w{2})-(\w{2}) (\w)/, '$3/$2/$1 $4')}</dd>

					<dt className="col-sm-3">Status</dt>
					<dd className="col-sm-9">{financeiro.status}</dd>

					<dt className="col-sm-3">Valor</dt>
					<dd className="col-sm-9">R$ {financeiro.total_parceiro}</dd>
				</dl>

				<h3>Agendamentos</h3>
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Número</th>
								<th>Cliente CPF</th>
								<th>Total</th>
								<th>Status</th>
							</tr>
						</thead>
						<tbody>
							{
								financeiro.agendamentos && financeiro.agendamentos.map(agendamento => 
									<tr key={`agendamento${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/cliente/v/${agendamento.id_cliente}`}>{agendamento.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</Link></td>
										<td>{`R$ ${agendamento.valor_parceiro}`}</td>
										<td>{agendamento.status}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
				<FloatButton icon="pencil-alt" title="Editar" />
			</div>
		);
	}
}

export default FinanceiroSingle;