import React from 'react';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class CaixaSingle extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            data_abertura: '',
            valor_abertura: '',
            data_fechamento: '',
			valor_fechamento: '',
			total_cartao: '',
            id_usuario: '',
            usuario_nome: '',
			pagamentos: [],
			entradas: '',
			saidas: '',
            isLoaded: false
        }

        this.fechar = this.fechar.bind(this);
    }

    componentDidMount() {
        if (this.props.match && this.props.match.params.id) {

			axiosAuth.get(`caixa/${this.props.match.params.id}`)
				.then(response => {
					var entradas = 0,
						saidas = 0;
					
					this.setState({ ...response.data[0] });

					response.data[0].pagamentos.forEach(pagamento => {
						if (pagamento.valor_pago_especie) {
							if (pagamento.valor_pago_especie > 0) {
								entradas += Number(pagamento.valor_pago_especie) - Number(pagamento.valor_troco);
							} else {
								saidas += Number(pagamento.valor_pago_especie);
							}
						}
					});

					this.setState({isLoaded: true, entradas: entradas.toFixed(2), saidas: saidas.toFixed(2)});
				});
		}
    }

    fechar() {
        const confirma = window.confirm('Tem certeza que deseja FECHAR O CAIXA?');
		if (confirma) {
            axiosAuth.put(`caixa/fechar`, {id_caixa: this.props.match.params.id})
				.then(response => {
					if (response.data.status !== false) {
						window.location.reload();
					} else {
						let message = 'Ocorreu um erro ao tentar fechar o caixa! Por favor tente novamente.';
						alert(response.data.message || message);
					}
				})
				.catch(err => {
					alert('Ocorreu um erro ao processar a requisição! Por favor, tente novamente.');
				});
        }
	}

    render() {
        return (
            <div>
                {this.state.isLoaded ? '' : <Load />}
                <div className="row">
					<div className="col-sm-9">
						<dl className="row">
							<dt className="col-sm-3">Data de abertura</dt>
							<dd className="col-sm-9">{this.state.data_abertura.replace(/^(\d{4})-(\d{2})-(\d{2}) (.+)/, '$3/$2/$1 $4')}</dd>
                            <dt className="col-sm-3">Valor de abertura</dt>
							<dd className="col-sm-9">R$ {this.state.valor_abertura.replace('.', ',')}</dd>
                            <dt className="col-sm-3">Data de fechamento</dt>
							<dd className="col-sm-9">{this.state.data_fechamento && this.state.data_fechamento.replace(/^(\d{4})-(\d{2})-(\d{2}) (.+)/, '$3/$2/$1 $4')}</dd>
                            <dt className="col-sm-3">Valor de fechamento</dt>
							<dd className="col-sm-9">{this.state.valor_fechamento ? `R$ ${this.state.valor_fechamento.replace('.', ',')}` : ''}</dd>
							<dt className="col-sm-3">Total pago no cartão</dt>
							<dd className="col-sm-9">{this.state.total_cartao ? `R$ ${this.state.total_cartao.replace('.', ',')}` : ''}</dd>
                            <dt className="col-sm-3">Funcionário</dt>
							<dd className="col-sm-9"><Link to={`/usuario/v/${this.state.id_usuario}`}>{this.state.usuario_nome}</Link></dd>
                            <dt className="col-sm-3">Entradas em espécie</dt>
							<dd className="col-sm-9">R$ {this.state.entradas.replace('.', ',')}</dd>
                            <dt className="col-sm-3">Saídas em espécie</dt>
							<dd className="col-sm-9">R$ {this.state.saidas.replace('.', ',')}</dd>
                        </dl>
                    </div>
                </div>

                <h3>Pagamentos</h3>
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Agendamentos</th>
								<th>Pago em espécie</th>
								<th>Pago no cartão</th>
								<th>Adicional do cartão</th>
								<th>Troco</th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.pagamentos.map(pagamento => 
									<tr key={`pagamento${pagamento.id_agendamento_pagamento}`} className={`status-${pagamento.valor_pago_especie >= 0? '1':'cancelado'}`}>
										<td>{pagamento.agendamentos_codigos}</td>
										<td>{`R$ ${pagamento.valor_pago_especie || '0.00'}`}</td>
										<td>{`R$ ${pagamento.valor_pago_cartao || '0.00'}`}</td>
										<td>{`R$ ${pagamento.valor_adicional_cartao || '0.00'}`}</td>
										<td>{`R$ ${pagamento.valor_troco}`}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
                <FloatButton onClick={this.fechar} title="Fechar caixa" icon="money-check-alt" />
            </div>
        );
    }
}

export default CaixaSingle;