import React from 'react';
import { Tooltip } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import FileSaver from 'file-saver';

const reportUrl = 'relatorio/procedimento';

class RelatorioProcedimento extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
            procedimentos: [],
			searchOptions: [
				{name: 'Nome', value: 'nome'},
			],
			searchFilters: [
				{name: 'Tipo', value: 'tipo', type: 'select', options: [{value: 'consulta', label: 'Consulta'}, {value: 'exame', label: 'Exame'}, {value: 'procedimento', label: 'Procedimento'}]},
				{name: 'Status do Agendamento', value: 'status', type: 'select', options: [{value: 'pendente', label: 'Pendente'}, {value: 'realizado', label: 'Realizado'}, {value: 'pago', label: 'Pago'}]},
				{name: 'Data inicial', value: 'data_inicial', props: {type: 'date'}},
				{name: 'Data final', value: 'data_final', props: {type: 'date'}},
			],
            search: {
                value: '',
                field: '',
                filters: ''
            },
			activeFilter: '',
			isLoaded: false
		};

		this.filter = this.filter.bind(this);
        this.handleDownload = this.handleDownload.bind(this);
	}

	componentDidMount() {	
        axiosAuth.get(reportUrl)
            .then(response => {
                this.setState({ isLoaded: true });
                if (response.data.status !== false) {
                    this.setState({ procedimentos: response.data });
                }
            });
	}

    handleDownload() {
		const url = `${reportUrl}${this.state.activeFilter ? this.state.activeFilter + '&' : '?'}export=csv`; 
        axiosAuth.get(url)
            .then((response) => {
                const BOM = '\uFEFF';
                const csvArray = response.data.replaceAll('.', ',') + BOM;
                var blob = new Blob([csvArray], {type: 'text/csv;charset=utf-8'});
                FileSaver.saveAs(blob, "relatorio.csv");
            });
    }

	filter(value, field, filters) {
		const activeFilter = value ? `/${value}/${field}?${filters}` : `?${filters}`;
		const url = `${reportUrl}${activeFilter}`; 
		this.setState({ isLoaded: false, activeFilter, search: { value, field, filters } });
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.setState({ procedimentos: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Procedimento</th>
								<th>Quantidade</th>
                                <th><Tooltip title='Valor dos procedimentos pagos aos parceiros'>Parceiro Pago</Tooltip></th>
                                <th><Tooltip title='Valor dos procedimentos realizados não pagos aos parceiros'>Parceiro Pendente</Tooltip></th>
                                <th><Tooltip title='Valor dos procedimentos pendentes de realização nos parceiros'>Parceiro Previsto</Tooltip></th>
                                <th><Tooltip title='Somatório dos procedimentos dos parceiros'>Parceiro Total</Tooltip></th>
                                <th><Tooltip title='Valor da CF dos procedimentos pagos aos parceiros'>CF Pago</Tooltip></th>
                                <th><Tooltip title='Valor da CF dos procedimentos realizados não pagos aos parceiros'>CF Pendente</Tooltip></th>
                                <th><Tooltip title='Valor da CF dos procedimentos pendentes de realização nos parceiros'>CF Previsto</Tooltip></th>
                                <th><Tooltip title='Somatório da CF dos procedimentos dos parceiros'>CF Total</Tooltip></th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.procedimentos.map(procedimento => (
									<tr>
                                        <td>{procedimento.procedimento}</td>
                                        <td>{procedimento.quantidade}</td>
                                        <td>R$&nbsp;{procedimento.valor_parceiro_pago}</td>
                                        <td>R$&nbsp;{procedimento.valor_parceiro_pendente}</td>
                                        <td>R$&nbsp;{procedimento.valor_parceiro_previsto}</td>
                                        <td>R$&nbsp;{procedimento.valor_parceiro_total}</td>
                                        <td>R$&nbsp;{procedimento.valor_cf_pago}</td>
                                        <td>R$&nbsp;{procedimento.valor_cf_pendente}</td>
                                        <td>R$&nbsp;{procedimento.valor_cf_previsto}</td>
                                        <td>R$&nbsp;{procedimento.valor_cf_total}</td>
                                    </tr>
								))
							}
						</tbody>
					</table>
				</div>
                <FloatButton onClick={this.handleDownload} title='Download' icon='download' />
			</div>
		);
	}
}

export default RelatorioProcedimento;