import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import axiosAuth from '../../helpers/axiosAuth';

import { Button, FormText, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import Select from 'react-select';

import PFForm from '../pessoaFisica/pfForm';
import PJForm from '../pessoaJuridica/pjForm';
import EnderecoForm from '../endereco/enderecoForm';

import { getParceiroPF, clearParceiroPF, toggleCPF, getParceiroPJ, clearParceiroPJ, toggleCNPJ } from './parceiroActions';
import { handleChange, handleSelectChange, clearForm, handleUpdate } from '../form/formActions';
import { alertErro } from '../layout/modalAlertActions';
import { clearState } from '../../ducks/pages';

class ParceiroForm extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			bancosOptions: [
				{label: 'Banco Bradesco', value: 'Banco Bradesco'},
				{label: 'Caixa Econômica Federal', value: 'Caixa Econômica Federal'},
				{label: 'Banco Citibank', value: 'Banco Citibank'},
				{label: 'Banco do Brasil', value: 'Banco do Brasil'},
				{label: 'Banco do Nordeste', value: 'Banco do Nordeste'},
				{label: 'Banco Itaú', value: 'Banco Itaú'},
				{label: 'Banco Mercantil do Brasil', value: 'Banco Mercantil do Brasil'},
				{label: 'Banco Pan', value: 'Banco Pan'},
				{label: 'Banco Real', value: 'Banco Real'},
				{label: 'Banco Rendimento', value: 'Banco Rendimento'},
				{label: 'Banco Rural', value: 'Banco Rural'},
				{label: 'Banco Safra', value: 'Banco Safra'},
				{label: 'Banco Santander', value: 'Banco Santander'},
				{label: 'Nuconta', value: 'Nuconta'},
				{label: 'HSBC Bank Brasil – Banco Múltiplo', value: 'HSBC Bank Brasil – Banco Múltiplo'}
			],
			disabled: props.disabled
		};

		this.addParceiro = this.addParceiro.bind(this);
		this.cbCPFCNPJ = this.cbCPFCNPJ.bind(this);
		// this.handleCpfChange = this.handleCpfChange.bind(this);
		// this.handleCnpjChange = this.handleCnpjChange.bind(this);
	}

	componentDidMount() {
		this.props.clear('PARCEIRO');
	}

	// handleCpfChange(event) {
	// 	const cpf = event.target.value;
		
	// 	this.props.change(event);
		
	// 	if (cpf.length === 14) {
	// 		this.props.toggleCPF();
	// 		this.props.getParceiroPF(cpf);
	// 	} else {
	// 		this.props.clearParceiroPF();
	// 	}
	// }

	// handleCnpjChange(event) {
	// 	const cnpj = event.target.value;
		
	// 	this.props.change(event);
		
	// 	if (cnpj.length === 18) {
	// 		this.props.toggleCNPJ();
	// 		this.props.getParceiroPJ(cnpj);
	// 	} else {
	// 		this.props.clearParceiroPJ();
	// 	}
	// }

	cbCPFCNPJ(data) {
		if (data.id_parceiro) {
			alert('PARCEIRO JÁ CADASTRADO');
			window.location.href = `/#/parceiro/v/${data.id_parceiro}`;
		} else if (data.id_pessoa_fisica) {
			this.props.change({target:{name: 'disabledPF'}}, true);
			this.props.change({target:{name: 'id_pessoa'}}, data.id_pessoa);
			this.props.update(data.pf, 'pf');
		} else if (data.id_pessoa_juridica) {
			this.props.change({target:{name: 'disabledPJ'}}, true);
			this.props.change({target:{name: 'id_pessoa'}}, data.id_pessoa);
			this.props.update(data.pj, 'pj');
		}
	}

	addParceiro(event) {
		event.preventDefault();

		if (this.state.disabled) {
			return false;
		}

		const parceiro = {
			nome: this.props.nome,
			tipo: this.props.tipo,
			banco: this.props.banco.value,
			agencia: this.props.agencia,
			conta: this.props.conta,
			horario_funcionamento: this.props.horario_funcionamento,
			ciclo_pagamento: this.props.ciclo_pagamento,
			email: this.props.email,
			senha: this.props.senha,
			senha_confirma: this.props.senha_confirma,
			endereco: {...this.props.endereco},
			id_pessoa: this.props.id_pessoa
		};

		if (parceiro.tipo === '1') {
			parceiro['pj'] = {...this.props.pj};
		} else {
			parceiro['pf'] = {...this.props.pf};
		}

		this.setState({disabled: true});

		axiosAuth.post('parceiro', parceiro)
			.then(response => {
				if (response.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					this.props.clear('PARCEIRO');
					this.props.clearState('parceirosById');
					alert('Parceiro adicionado com sucesso!');
				}
				this.setState({disabled: false});
			})
			.catch(err => {
				if (err.response.data && err.response.data.erros) {
					this.props.alertErro(err.response.data.erros);
				} else {
					this.props.alertErro('Ocorreu um erro ao processar a requisição. Por favor, tente novamente!');
				}
				this.setState({disabled: false});
			});

	};

	valida() {
		//this.formEl is a reference in the component to the form DOM element.
		const formEl = this.formEl;
		const formLength = formEl.length;

		/*
		* The checkValidity() method on a form runs the 
		* html5 form validation of its elements and returns the result as a boolean.
		* It returns 'false' if at least one of the form elements does not qualify,
		* and 'true', if all form elements are filled with valid values.
		*/
		if (formEl.checkValidity() === false) {
			for (let i = 0; i < formLength; i++) {
				//the i-th child of the form corresponds to the forms i-th input element
				const elem = formEl[i];
				/*
				* errorLabel placed next to an element is the container we want to use 
				* for validation error message for that element
				*/
				const errorLabel = elem.parentNode.querySelector(".invalid-feedback");

				/*
				* A form element contains also any buttuns contained in the form.
				* There is no need to validate a button, so, we'll skip that nodes.
				*/
				if (errorLabel && elem.nodeName.toLowerCase() !== "button") {
					/*
					* Each note in html5 form has a validity property. 
					* It contains the validation state of that element.
					* The elem.validity.valid property indicates whether the element qualifies its validation rules or no.
					* If it does not qualify, the elem.validationMessage property will contain the localized validation error message.
					* We will show that message in our error container if the element is invalid, and clear the previous message, if it is valid.
					*/
					if (!elem.validity.valid) {
						errorLabel.textContent = elem.validationMessage;
					} else {
						errorLabel.textContent = "";
					}
				}
			}

			//Return 'false', as the formEl.checkValidity() method said there are some invalid form inputs.
			return false;
		} else {
			//The form is valid, so we clear all the error messages
			for (let i = 0; i < formLength; i++) {
				const elem = formEl[i];
				const errorLabel = elem.parentNode.querySelector(".invalid-feedback");
				if (errorLabel && elem.nodeName.toLowerCase() !== "button") {
					errorLabel.textContent = "";
				}
			}

			//Return 'true', as the form is valid for submission
			return true;
		}
	};


	render() {
		return (
			<form onSubmit={this.addParceiro}>
				<Row>
					<Col>
						<FormGroup>
							<Label for="nome">Nome</Label>
							<Input type="text" name="nome" id="nome" value={this.props.nome} onChange={this.state.disabled || this.props.change} disabled={this.state.disabled} required />
							<FormText>Nome que será mostrado para os usuários durante a pesquisa.</FormText>
						</FormGroup>
					</Col>
					<Col>
						<FormGroup>
							<Label for="tipo">Tipo</Label>
							<Input type="select" name="tipo" id="tipo" value={this.props.tipo} onChange={this.state.disabled || this.props.change} disabled={this.state.disabled} required>
								<option value="">Escolha um tipo de pessoa</option>
								<option value="0">Pessoa Física</option>
								<option value="1">Pessoa Jurídica</option>
							</Input>
						</FormGroup>
					</Col>
				</Row>
				<Row>
					<Col>
						<FormGroup>
							<Label for="horario_funcionamento">Horário de funcionamento</Label>
							<Input type="text" name="horario_funcionamento" id="horario_funcionamento" value={this.props.horario_funcionamento} onChange={this.state.disabled || this.props.change} disabled={this.state.disabled} required />
							<FormText>Ex: 07:00 as 18:00</FormText>
						</FormGroup>
					</Col>
					<Col sm="3">
						<FormGroup>
							<Label for="ciclo_pagamento">Ciclo de Pagamento</Label>
							<Input type="select" name="ciclo_pagamento" id="ciclo_pagamento" value={this.props.ciclo_pagamento} onChange={this.state.disabled || this.props.change} disabled={this.state.disabled} required>
								<option value="30">30 dias</option>
								<option value="15">15 dias</option>
								<option value="7">7 dias</option>
							</Input>
						</FormGroup>
					</Col>
				</Row>
				{ 
				this.props.tipo === '0' ? 
					<div>
						<PFForm pf={this.props.pf} change={this.props.change} cbCPF={this.cbCPFCNPJ} disabled={this.state.disabled || this.props.disabledPF} />
					</div>
				: this.props.tipo === '1' ?
					<div>
						<PJForm pj={this.props.pj} change={this.props.change} cbCNPJ={this.cbCPFCNPJ} disabled={this.state.disabled || this.props.disabledPJ} />
					</div>
				: ''
				}
				<fieldset>
					<legend>Dados Bancários</legend>
					<Row>
						<Col>
							<FormGroup>
								<Label for="banco">Banco</Label>
								<Select options={this.state.bancosOptions} value={this.props.banco} onChange={(selectedOption) => { this.props.changeSelect('banco', selectedOption) }} type="text" name="banco" id="banco" placeholder="Selecione..." noResultsText="Nenhum banco encontrado" required disabled={this.state.disabled} />
								<FormText>Conta utilizada para depósito.</FormText>
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="agencia">Agência</Label>
								<Input type="text" name="agencia" id="agencia" value={this.props.agencia} onChange={this.props.change} required disabled={this.state.disabled} />
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="conta">Conta</Label>
								<Input type="text" name="conta" id="conta" value={this.props.conta} onChange={this.props.change} required disabled={this.state.disabled} />
							</FormGroup>
						</Col>
					</Row>
				</fieldset>
				{
					this.props.id_pessoa ? '' : (
						<fieldset>
							<legend>Dados para Acesso</legend>
							<Row>
								<Col>
									<FormGroup>
										<Label for="email_usuario">E-mail</Label>
										<Input type="email" name="email" id="email_usuario" value={this.props.email} onChange={this.props.change} required />
										<FormText>E-mail utilizado para login.</FormText>
									</FormGroup>
								</Col>
								<Col>
									<FormGroup>
										<Label for="senha">Senha</Label>
										<Input type="password" name="senha" id="senha" minLength="6" value={this.props.senha} onChange={this.props.change} required />
									</FormGroup>
								</Col>
								<Col>
									<FormGroup>
										<Label for="senha_confirma">Confirma Senha</Label>
										<Input type="password" name="senha_confirma" id="senha_confirma" minLength="6" value={this.props.senha_confirma} onChange={this.props.change} required />
									</FormGroup>
								</Col>
							</Row>
						</fieldset>
					)
				}
				{
					this.props.tipo === '' ? '' : 
					<EnderecoForm endereco={this.props.endereco} change={this.props.change} disabled={this.state.disabled} />
				}
				<Button type="submit" outline color="primary" disabled={this.state.disabled}>Salvar</Button>
			</form>
		);
	}
}

const mapStateToProps = (state) => ({
	email: state.parceiroForm.email,
	senha: state.parceiroForm.senha,
	senha_confirma: state.parceiroForm.senha_confirma,
	nome: state.parceiroForm.nome,
	banco: state.parceiroForm.banco,
	agencia: state.parceiroForm.agencia,
	conta: state.parceiroForm.conta,
	horario_funcionamento: state.parceiroForm.horario_funcionamento,
	ciclo_pagamento: state.parceiroForm.ciclo_pagamento,
	imagem: state.parceiroForm.imagem,
	tipo: state.parceiroForm.tipo,
	cpf: state.parceiroForm.cpf,
	pf: state.parceiroForm.pf,
	pj: state.parceiroForm.pj,
	endereco: state.parceiroForm.endereco,
	id_pessoa: state.parceiroForm.id_pessoa,
	disabledPF: state.parceiroForm.disabledPF,
	disabledPJ: state.parceiroForm.disabledPJ,
	disabledCPF: state.parceiroForm.disabledCPF,
	disabledCNPJ: state.parceiroForm.disabledCNPJ,
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		getParceiroPF,
		clearParceiroPF,
		toggleCPF,
		getParceiroPJ,
		clearParceiroPJ,
		toggleCNPJ,
		change: handleChange('PARCEIRO'),
		changeSelect: handleSelectChange('PARCEIRO'),
		update: handleUpdate('PARCEIRO'),
		clear: clearForm,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ParceiroForm);