import React from 'react';
import { NavLink } from 'react-router-dom';
import { Spin } from 'antd';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import axiosAuth from '../../helpers/axiosAuth';

class Sidebar extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			menu: [],
			loading: true
		};
	}

	componentDidMount() {
		axiosAuth.get('/menu')
			.then(response => {
				if (response.data.status !== false) {
					this.setState({menu: response.data, loading: false});
				}
			});
	}

	toggleMenu() {
		document.querySelector('.sidebar').classList.toggle('show-menu');
	}

	render(){
		return (
			<nav className="col-md-3 col-xl-2 sidebar">
				<div className="sidebar-sticky">
					<div className="header-nav">
						<h1 className="logo"><a href="/"><img src="logo-white.png" alt="Consulta Fácil" /></a></h1>
						<button onClick={this.toggleMenu} className="btn-nav">
							<span className="icon-bar top-bar"></span>
							<span className="icon-bar middle-bar"></span>
							<span className="icon-bar bottom-bar"></span>
						</button>
					</div>
					<Spin spinning={this.state.loading}>
						<ul className="nav flex-column">
							{
								this.state.menu.map(menu_item => (
									<li className="nav-item" key={`menu-${menu_item.id_menu}`}>
										<NavLink className="nav-link" activeClassName="active" to={menu_item.link}>
											<FontAwesomeIcon icon={menu_item.icone} fixedWidth /> <span>{menu_item.nome}</span>
										</NavLink>
										{
											(menu_item.submenus && menu_item.submenus.length) ? 
											<ul className="nav-sub">
												{
													menu_item.submenus.map(sub => (
														<li className="nav-item" key={`menu-${sub.id_menu}`}>
															<NavLink className="nav-link" activeClassName="active" to={sub.link} exact={sub.exact ? true:false}>{sub.nome}</NavLink>
														</li>
													))
												}
											</ul> : ''
										}
									</li>
								))
							}
						</ul>
					</Spin>
					<div className="nav-bottom d-flex">
						<NavLink className="nav-link" to="/logout" title="Sair">
							<FontAwesomeIcon icon="sign-out-alt" transform="grow-5" />
						</NavLink>
						<NavLink className="nav-link" to="/meus-dados" title="Meus dados">
							<FontAwesomeIcon icon="user-circle" transform="grow-5" /> {localStorage.getItem('email')}
						</NavLink>
					</div>
				</div>
			</nav>
		);
	}
}

export default Sidebar;