import React from 'react';
import { Button, FormText, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import InputMask from 'react-input-mask';
import axiosAuth from '../../helpers/axiosAuth';
import Select from 'react-select';

class RepresentanteForm extends React.Component {

    constructor(props) {
		super(props);

		this.state = {
            nome: '',
            cpf: '',
            telefones: '',
            banco: '',
            agencia: '',
            conta: '',
			bancosOptions: [
				{label: 'Banco Bradesco', value: 'Banco Bradesco'},
				{label: 'Caixa Econômica Federal', value: 'Caixa Econômica Federal'},
				{label: 'Banco Citibank', value: 'Banco Citibank'},
				{label: 'Banco do Brasil', value: 'Banco do Brasil'},
				{label: 'Banco do Nordeste', value: 'Banco do Nordeste'},
				{label: 'Banco Itaú', value: 'Banco Itaú'},
				{label: 'Banco Mercantil do Brasil', value: 'Banco Mercantil do Brasil'},
				{label: 'Banco Pan', value: 'Banco Pan'},
				{label: 'Banco Real', value: 'Banco Real'},
				{label: 'Banco Rendimento', value: 'Banco Rendimento'},
				{label: 'Banco Rural', value: 'Banco Rural'},
				{label: 'Banco Safra', value: 'Banco Safra'},
				{label: 'Banco Santander', value: 'Banco Santander'},
				{label: 'Nuconta', value: 'Nuconta'},
				{label: 'HSBC Bank Brasil – Banco Múltiplo', value: 'HSBC Bank Brasil – Banco Múltiplo'}
			],
			disabled: props.disabled
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSelectChange = this.handleSelectChange.bind(this);
        this.addRepresentante = this.addRepresentante.bind(this);
    }

    handleChange(event) {
        if (this.state.disabled) {
            return false;
        }
        this.setState({[event.target.name]: event.target.value});
    }

    handleSelectChange(name, selectedOption) {
        this.setState({[name]: selectedOption ? selectedOption : null});
    }

    addRepresentante(event) {
        event.preventDefault();
        this.setState({disabled: true});

        const representante = {
            nome: this.state.nome,
            cpf: this.state.cpf.replace(/\D/g, ''),
            telefones: this.state.telefones,
            banco: this.state.banco.value,
            agencia: this.state.agencia,
            conta: this.state.conta
        };

        axiosAuth.post('representante', representante)
            .then(response => {
                if (response.data.status === false) {
                    alert(response.data.message);
                } else {
                    alert('Representante cadastrado com sucesso!');
                    window.location.href = `/#/representante`;
                }
                this.setState({disabled: false});
            })
            .catch(err => {
                alert('Ocorreu um erro ao processar sua requisição! Por favor tente novamente.')
                this.setState({disabled: false});
            });
    }

    render() {
        return (
            <form onSubmit={this.addRepresentante}>
                <FormGroup>
                    <Label for="nome">Nome</Label>
                    <Input type="text" name="nome" id="nome" value={this.state.nome} onChange={this.handleChange} disabled={this.state.disabled} required />
                </FormGroup>
                <Row>
					<Col sm="4">
						<FormGroup>
							<Label for="cpf">CPF</Label>
							{/* <Input type="text" name="cpf" id="cpf" value={this.state.cpf} onChange={this.handleChange} disabled={this.state.disabled} /> */}
                            <InputMask mask="999.999.999-99" maskChar="" name="cpf" id="cpf" className="form-control" value={this.state.cpf} onChange={this.handleChange} disabled={this.state.disabled} />
						</FormGroup>
					</Col>
                    <Col>
						<FormGroup>
							<Label for="telefones">Telefone(s)</Label>
							<Input type="text" name="telefones" id="telefones" value={this.state.telefones} onChange={this.handleChange} disabled={this.state.disabled} />
						</FormGroup>
					</Col>
                </Row>
                <fieldset>
					<legend>Dados Bancários</legend>
					<Row>
						<Col>
							<FormGroup>
								<Label for="banco">Banco</Label>
								<Select options={this.state.bancosOptions} value={this.state.banco} onChange={(selectedOption) => { this.handleSelectChange('banco', selectedOption) }} type="text" name="banco" id="banco" placeholder="Selecione..." noResultsText="Nenhum banco encontrado" required disabled={this.state.disabled} />
								<FormText>Conta utilizada para depósito.</FormText>
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="agencia">Agência</Label>
								<Input type="text" name="agencia" id="agencia" value={this.state.agencia} onChange={this.handleChange} required disabled={this.state.disabled} />
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="conta">Conta</Label>
								<Input type="text" name="conta" id="conta" value={this.state.conta} onChange={this.handleChange} required disabled={this.state.disabled} />
							</FormGroup>
						</Col>
					</Row>
				</fieldset>
                <Button type="submit" outline color="primary">Salvar</Button>
            </form>
        );
    }
}

export default RepresentanteForm;