import React from 'react';
import { FormGroup, Label, Input, Button } from 'reactstrap';
import axiosAuth from '../../helpers/axiosAuth';

class CaixaForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            valor_abertura: 0,
            disabled: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.addCaixa = this.addCaixa.bind(this);
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    addCaixa(event) {
        event.preventDefault();
        if (this.state.disabled) {
			return false;
		}
        this.setState({disabled: true});

        const caixa = {
            valor_abertura: this.state.valor_abertura
        };

        axiosAuth.post('caixa', caixa)
            .then(response => {
                if (response.data.status === false) {
					alert(response.data.message);
				} else {
                    alert('Caixa aberto com sucesso!');
				}
                window.location.href = `/#/caixa`;
            })
            .catch(err => {
                alert('Ocorreu um erro ao processar sua requisição! Por favor tente novamente.')
				this.setState({disabled: false});
            });
    }

    render() {
        return (
            <form onSubmit={this.addCaixa}>
                <FormGroup>
                    <Label for="valor_abertura">Valor de abertura</Label>
                    <Input type="text" name="valor_abertura" id="valor_abertura" value={this.state.valor_abertura} required onChange={this.handleChange} disabled={this.state.disabled} />
                </FormGroup>
                <Button type="submit" outline color="primary">Salvar</Button>
            </form>
        );
    }
}

export default CaixaForm;