import React from 'react';
import { Collapse, Label, Input, Row, Col } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import InputMask from 'react-input-mask';
import Select from 'react-select'

export default class Search extends React.Component {

	constructor(props) {
		super(props);

		const firstOption = this.props.options[0];

		this.state = {
			InputTag: firstOption.mask ? InputMask : Input,
			inputOptions: firstOption.mask ? {...firstOption, maskChar: firstOption.maskChar || ''} : firstOption,
			value: '',
			collapse: false,
			filtersValues: props.filters ? props.filters.reduce((fv, f) => ({...fv, [f.value]: ''}), {}) : ''
		};

		this.changeFilter = this.changeFilter.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleFilterChange = this.handleFilterChange.bind(this);
		this.toggle = this.toggle.bind(this);
	}

	changeFilter(event) {
		const { value } = event.target;
		const option = this.props.options.find(o => o.value === value);

		if (option.mask) {
			this.setState({
				InputTag: InputMask,
				inputOptions: {...option, maskChar: option.maskChar || ''}
			})
		} else {
			this.setState({
				InputTag: Input,
				inputOptions: option
			});
		}
	}

	handleChange(event) {
		this.setState({value: event.target.value});
	}

	handleFilterChange(event, selectedOption) {
		const name = event.target ? event.target.name : event;
		const value = event.target ? event.target.value : selectedOption ? selectedOption : '';
		this.setState({ filtersValues: {...this.state.filtersValues, [name]: value} });
	}

	toggle() {
		this.setState({collapse: !this.state.collapse});
	}

	render() {
		const { InputTag } = this.state;
		const { name, value, ...options } = this.state.inputOptions;

		return (
			<div className="search mb-4">
				<Row>
					<Col sm="3">
						<Label for="search_tipo">Filtrar por:</Label>
						<Input type="select" id="search_tipo" name="search_tipo" onChange={this.changeFilter}>
							{
								this.props.options.map((option, key) => 
									<option key={`searchopt${option.value}`} value={option.value}>{option.name}</option>
								)
							}
						</Input>
					</Col>
					<Col>
						<Label>&nbsp;</Label>
						<div className="input-group">
							<InputTag type="text" value={this.state.value} className="form-control" onChange={this.handleChange} placeholder="Digite o filtro..." {...options} />
							<div className="input-group-append">
								{
									this.props.filters ? 
										<button onClick={this.toggle} className="btn btn-outline-secondary" title="Filtros"><span className="sr-only">Filtros</span><FontAwesomeIcon icon="filter" /></button>
									: ''
								}
								<button onClick={ () => this.props.onSearch(this.state.value.replace(/\.|\/|-/g, ''), this.state.inputOptions.value, Object.keys(this.state.filtersValues).map(key => `${key}=${this.state.filtersValues[key].value? this.state.filtersValues[key].value : this.state.filtersValues[key]}`).join('&')) } className="btn btn-outline-secondary" type="button"><FontAwesomeIcon icon="search" /> Pesquisar</button>
							</div>
						</div>
					</Col>
				</Row>
				{
					this.props.filters ? 
						<Collapse isOpen={this.state.collapse} className="search-filtros">
							<Row>
								{
									this.props.filters.map(f => (
										<Col key={`filter_${f.value}`}>
											<Label htmlFor={`filter_${f.value}`}>{f.name}</Label>
											{
											f.type === 'select' ?
												<Select id={`filter_${f.value}`} name={f.value} options={f.options} value={this.state.filtersValues[f.value]} onChange={(selectedOption) => this.handleFilterChange(f.value, selectedOption)} {...f.props} />
											:
												<Input type="text" id={`filter_${f.value}`} name={f.value} value={this.state.filtersValues[f.value]} onChange={this.handleFilterChange} {...f.props} />
											}
										</Col>
									))
								}
							</Row>
						</Collapse>
					: ''
				}
			</div>
		);
	}
}
