import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import Load from '../layout/load';

class RepresentanteSingle extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            representante: {},
            isLoaded: false
        };
    }

    componentDidMount() {
        if (this.props.match && this.props.match.params.id) {
            axiosAuth.get(`representante/${this.props.match.params.id}`)
                .then(response => {
                    this.setState({isLoaded: true, representante: response.data[0]});
                })
                .catch(err => {
                    this.setState({isLoaded: true});
                    alert('Ocorreu um erro ao carregar a página. Por favor, tente novamente!');
                });
        }
    }

    render() {
        const { representante } = this.state;

		return (
			<div>
                {this.state.isLoaded || <Load />}
				<dl className="row">
					<dt className="col-sm-3">Nome</dt>
					<dd className="col-sm-9">{representante.nome}</dd>

					<dt className="col-sm-3">CPF</dt>
					<dd className="col-sm-9">{representante.cpf && representante.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</dd>

					<dt className="col-sm-3">Telefones</dt>
					<dd className="col-sm-9">{representante.telefones}</dd>

					<dt className="col-sm-3">Banco</dt>
					<dd className="col-sm-9">{representante.banco}</dd>

					<dt className="col-sm-3">Agência</dt>
					<dd className="col-sm-9">{representante.agencia}</dd>

					<dt className="col-sm-3">Conta</dt>
					<dd className="col-sm-9">{representante.conta}</dd>
				</dl>
			</div>
		);
	}
}

export default RepresentanteSingle;