import React from 'react';
import Page from '../layout/page';

import ProcedimentoList from './procedimentoList';
import ProcedimentoForm from './procedimentoForm';
import ProcedimentoFormEdit from './procedimentoFormEdit';

const icon = 'notes-medical';
const title = 'Procedimentos';

export const ProcedimentoIndex = (props) => (
	<Page title={title} icon={icon}>
		<ProcedimentoList />
	</Page>
);

export const ProcedimentoAdd = (props) => (
	<Page title={title} icon={icon}>
		<ProcedimentoForm />
	</Page>
);

export const ProcedimentoEdit = ({props, match}) => (
	<Page title={title} icon={icon}>
		<ProcedimentoFormEdit match={match} />
	</Page>
);