import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class CaixaList extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
			searchOptions: [
				{name: 'CPF/Matrícula', value: 'cpf', mask: '999.999.999-99'},
				{name: 'Nome', value: 'nome'}
			],
			isLoaded: true
		};
    }

    componentDidMount() {
        if (!this.props.caixas.length) {
            this.setState({ isLoaded: false });
            axiosAuth.get('caixa')
                .then(response => {
                    this.setState({ isLoaded: true });
                    if (response.data.status !== false) {
                        this.props.changeState({ caixas: response.data });
                    }
                });
        }
    }

    render() {
        return (
            <div>
                {this.state.isLoaded ? '' : <Load />}
                <div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Data de abertura</th>
								<th>Valor de abertura</th>
								<th>Data de fechamento</th>
								<th>Valor de fechamento</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.caixas.map(caixa => 
									<tr key={`caixa${caixa.id_caixa}`}>
										<td><Link to={`/caixa/v/${caixa.id_caixa}`}>{caixa.data_abertura.replace(/^(\d{4})-(\d{2})-(\d{2}) (.+)/, '$3/$2/$1 $4')}</Link></td>
										<td>R$ {caixa.valor_abertura.replace('.', ',')}</td>
										<td>{caixa.data_fechamento && caixa.data_fechamento.replace(/^(\d{4})-(\d{2})-(\d{2}) (.+)/, '$3/$2/$1 $4')}</td>
										<td>{caixa.valor_fechamento ? `R$ ${caixa.valor_fechamento.replace('.', ',')}` : ''}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
                <FloatButton onClick={false} to="/caixa/adicionar" />
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    caixas: state.pages.caixas
});

const mapDispatchToProps = (dispatch) => (
    bindActionCreators({
        changeState,
        clearState
    }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(CaixaList);