import axiosAuth from '../../helpers/axiosAuth';

export const filterAgendamentosByParceiro = (selectedOption, cb=()=>{}) => {
	return dispatch => {
		if (!selectedOption) {
			dispatch({type: 'FINANCEIRO_FILTER_AGENDAMENTOS', payload: []});
		} else {
			axiosAuth.get(`agendamento/realizados/${selectedOption.value}`)
				.then(response => {
					if (response.data.status === false) {
						dispatch({type: 'FINANCEIRO_FILTER_AGENDAMENTOS', payload: []});
					} else {
						dispatch({type: 'FINANCEIRO_FILTER_AGENDAMENTOS', payload: response.data});
					}
					cb();
				})
		}
	};
}

export const checkAgendamento = (event) => {
	return {type: 'FINANCEIRO_AGENDAMENTO_CHECK', payload: event.target.value};
}