import React from 'react';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import PFSingle from '../pessoaFisica/pfSingle';
import PJSingle from '../pessoaJuridica/pjSingle';
import EnderecoSingle from '../endereco/enderecoSingle';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class ParceiroSingle extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			nome: '',
			status: '',
			imagem: '',
			tipo: '',
			horario_funcionamento: '',
			ciclo_pagamento: '-',
			data_fechamento_ciclo: '',
			pj: {},
			pf: {},
			endereco: {},
			email: '',
			isLoaded: false
		};
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`parceiro/${this.props.match.params.id}`)
				.then(response => {
					this.setState({ isLoaded: true, ...response.data });
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<dl className="row">
					<dt className="col-sm-3">Nome</dt>
					<dd className="col-sm-9">{this.state.nome}</dd>

					<dt className="col-sm-3">Status</dt>
					<dd className="col-sm-9">{['inativo', 'ativo'][this.state.status]}</dd>
		
					<dt className="col-sm-3">Funcionamento</dt>
					<dd className="col-sm-9">{this.state.horario_funcionamento}</dd>
		
					<dt className="col-sm-3">Ciclo de pagamento</dt>
					<dd className="col-sm-9">{this.state.ciclo_pagamento} dias</dd>
		
					<dt className="col-sm-3">Próximo ciclo</dt>
					<dd className="col-sm-9">{this.state.data_fechamento_ciclo.replace(/(\d{4})-(\d{2})-(\d{2})/, '$3/$2/$1')}</dd>
				</dl>
				{ 
					this.state.tipo === '0' ?
						<PFSingle pf={this.state.pf} />
					: this.state.tipo === '1' ?
						<PJSingle pj={this.state.pj} />
					: ''
				}
				<EnderecoSingle endereco={this.state.endereco} />
				<div>
					<h3>Dados de acesso</h3>
					<dl className="row">
						<dt className="col-sm-3">Email</dt>
						<dd className="col-sm-9">{this.state.email}</dd>
					</dl>
				</div>
				<FloatButton onClick={false} icon="pencil-alt" title="Editar" to={`/parceiro/e/${this.props.match.params.id}`} />
			</div>
		);
	}
}

export default ParceiroSingle;