import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import { Link } from 'react-router-dom';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class ClienteSingleAgendamentos extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			agendamentos: [],
			searchOptions: [
				{name: 'Nome', value: 'nome'},
				{name: 'Código', value: 'codigo', mask: '99999999'}
			],
			status: false,
			message: 'Carregando...',
			isLoaded: false
		};
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`cliente/${this.props.match.params.id}/agendamento`)
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status === false) {
						this.setState(response.data);
					} else {
						this.setState({agendamentos: response.data, status: true});
					}
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<table className="table">
					<thead>
						<tr>
							<th>Código</th>
							<th>Parceiro</th>
							<th>Data</th>
							<th>Valor</th>
							<th>Status</th>
						</tr>
					</thead>
					<tbody>
						{
							this.state.status ?
								this.state.agendamentos.map(agendamento => 
									<tr key={`cliage${agendamento.id_agendamento}`} className={`status-${agendamento.status}`}>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/parceiro/v/${agendamento.parceiro.id_parceiro}`}>{agendamento.parceiro.nome}</Link></td>
										<td>{agendamento.data_criacao.replace(/^(\d{4})-(\d{2})-(\d{2}) (\d)/, '$3/$2/$1 $4')}</td>
										<td>{`R$ ${agendamento.total.replace('.', ',')}`}</td>
										<td>{agendamento.status}</td>
									</tr>
								)
							:
								<tr>
									<td align="center" colSpan="5">{this.state.message}</td>
								</tr>
						}
					</tbody>
				</table>
				{
					this.state.status ?
						<FloatButton onClick={false} to={`/agendamento/adicionar/${this.state.agendamentos[0].cliente.cpf}`} />
					: ''
				}
			</div>
		)
	}
}

export default ClienteSingleAgendamentos;