import React from 'react';
import { Link } from 'react-router-dom';
import { Table } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import ParceiroProcedimentoForm from './parceiroProcedimentoForm';
import TableSearch from '../layout/tableSearch';
import MyModal from '../layout/myModal';
import Load from '../layout/load';

class ParceiroSingleProcedimentos extends TableSearch {

	constructor(props) {
		super(props);

		this.state = {
			procedimentos: [],
			columns: [
				{title: 'Código', dataIndex: 'codigo_tuss', key: 'codigo_tuss', ...this.getColumnSearchProps('codigo_tuss')},
				{title: 'Nome', dataIndex: 'nome', key: 'nome', ...this.getColumnSearchProps('nome'), render: (nome, row) => (<Link to={`/parceiro/v/${row.id_parceiro}/procedimentos/${row.id_parceiro_procedimento}`}>{nome}</Link>) },
				{title: 'Valor Parceiro', dataIndex: 'valor_parceiro', key: 'valor_parceiro', render: (valor_parceiro) => (`R$ ${valor_parceiro.replace('.', ',')}`)},
				{title: 'Status', dataIndex: 'status', key: 'status', render: (status) => (['inativo','ativo'][status])}
			],
			searchOptions: [
				{name: 'Nome', value: 'nome'},
				{name: 'Código', value: 'codigo', mask: '99999999'}
			],
			status: false,
			message: '',
			isLoaded: false
		};
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			axiosAuth.get(`parceiroprocedimento/${this.props.match.params.id}`)
				.then(response => {
					this.setState({isLoaded: true});
					if (response.data.status === false) {
						this.setState(response.data);
					} else {
						this.setState({procedimentos: response.data, status: true});
						
					}
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<MyModal title="Cadastro de Procedimento">
					<ParceiroProcedimentoForm parceiro={this.props.match.params.id} />
				</MyModal>
				<div className="table-responsive">
					<Table rowKey="id_parceiro_procedimento" columns={this.state.columns} dataSource={this.state.procedimentos} />
				</div>
			</div>
		);
	}
}

export default ParceiroSingleProcedimentos;