import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class ParceiroList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			searchOptions: [
				{name: 'Nome', value: 'nome'},
				{name: 'CPF', value: 'cpf', mask: '999.999.999-99'},
				{name: 'CNPJ', value: 'cnpj', mask: '99.999.999/9999-99'}
			],
			isLoaded: true
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (!this.props.parceirosById.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get('parceiro')
				.then(response => {
					this.setState({ isLoaded: true });
					if (response.data.status !== false) {
						this.props.changeState({ parceirosById: response.data });
					}
				});
		}
	}

	filter(value, field) {
		const url = value ? `parceiro/${value}/${field}` : 'parceiro';
		this.setState({ isLoaded: false });
		this.props.clearState('parceirosById');
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.props.changeState({ parceirosById: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead>
							<tr>
								<th>Nome</th>
								<th>CPF/CNPJ</th>
								<th>Tipo</th>
								<th>Status</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.parceirosById.map(parceiro => (
									<tr key={`parceiro${parceiro.id_parceiro}`} className={`status-${parceiro.status}`}>
										<td><Link to={`/parceiro/v/${parceiro.id_parceiro}`}>{parceiro.nome}</Link></td>
										<td>{parceiro.cpf ? parceiro.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4') : parceiro.cnpj ? parceiro.cnpj.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, '$1.$2.$3/$4-$5') : ''}</td>
										<td>{['Pessoa Física', 'Pessoa Jurídica'][parceiro.tipo]}</td>
										<td>{['inativo', 'ativo'][parceiro.status]}</td>
									</tr>
								))
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={false} to="/parceiro/adicionar" />
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	parceirosById: state.pages.parceirosById
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		changeState,
		clearState
	}, dispatch)
)

export default connect(mapStateToProps, mapDispatchToProps)(ParceiroList);