import React from 'react';
import { NavLink } from 'react-router-dom';
import Page from '../layout/page';

import ClienteList from './clienteList';
import ClienteForm from './clienteForm';
import ClienteFormEdit from './clienteFormEdit';
import ClienteSingle from './clienteSingle';
import ClienteSingleAgendamentos from './clienteSingleAgendamentos';

const icon = 'address-card';
const title = 'Clientes';

export const ClienteIndex = (props) => (
	<Page title={title} icon={icon}>
		<ClienteList />
	</Page>
);

export const ClienteAdd = (props) => (
	<Page title={title} icon={icon}>
		<ClienteForm />
	</Page>
);

export const ClienteEdit = ({props, match}) => (
	<Page title={title} icon={icon}>
		<ClienteFormEdit match={match} />
	</Page>
);

export class ClienteView extends React.Component { 

	constructor({props, match}) {
		super(props);

		this.state = { match };
	}

	render() {
		return (
			<Page title={title} icon={icon}>
				<ul className="nav nav-tabs">
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/cliente/v/${this.state.match.params.id}`}>
							Detalhes
						</NavLink>
					</li>
					<li className="nav-item">
						<NavLink className="nav-link" exact activeClassName="active" to={`/cliente/v/${this.state.match.params.id}/agendamentos`}>
							Agendamentos
						</NavLink>
					</li>
				</ul>
				<br/>
				{
					this.state.match.params.action === 'agendamentos' ?
					<ClienteSingleAgendamentos match={this.state.match} />
					:
					<ClienteSingle match={this.state.match} />
				}
			</Page>
		)
	}
};