import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import axiosAuth from '../../helpers/axiosAuth';
import { Button } from 'reactstrap';
import PFForm from '../pessoaFisica/pfForm';
import EnderecoForm from '../endereco/enderecoForm';
import { getCliente } from './clienteActions';
import { handleChange, handleUpdate, clearForm } from '../form/formActions';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';
import { alertErro } from '../layout/modalAlertActions';
import { clearState } from '../../ducks/pages';

class ClienteFormEdit extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			disabled: props.disabled
		};

		this.updateCliente = this.updateCliente.bind(this);
	}

	componentDidMount() {
		this.props.clear('CLIENTE');

		if (this.props.match && this.props.match.params.id) {
			this.props.getCliente(this.props.match.params.id, () => this.props.change('isLoaded', true));
		}
	}

	updateCliente(event) {
		event.preventDefault();
		if (this.state.disabled) {
			return false;
		}
		this.setState({disabled: true});

		const cliente = {
			id_cliente: this.props.match.params.id,
			pf: {...this.props.pf},
			endereco: {...this.props.endereco}
		};

		axiosAuth.put('cliente', cliente)
			.then(response => {
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
					this.setState({disabled: false});
				} else {
					this.props.clear('CLIENTE');
					this.props.clearState('clientesById');
					alert('Cliente atualizado com sucesso!');
					window.location.href = `/#/cliente/v/${this.props.match.params.id}`;
				}
			})
			.catch(err => {
				this.props.alertErro('Ocorreu um erro ao processar sua requisição. Por favor tente novamente!')
				this.setState({disabled: false});
			});
	};

	render() {
		return (
			<form onSubmit={this.updateCliente}>
				{this.props.isLoaded ? '' : <Load />}
				<PFForm pf={this.props.pf} change={this.state.disabled ? undefined : this.props.change} disabled={this.state.disabled} />
				<EnderecoForm endereco={this.props.endereco} change={this.state.disabled ? undefined : this.props.change} disabled={this.state.disabled} />
				<Button type="submit" outline color="primary" disabled={this.state.disabled}>Salvar</Button>
				<FloatButton onClick={false} icon="arrow-left" title="Voltar" to={`/cliente/v/${this.props.match.params.id}`} />
			</form>
		);
	}
}

const mapStateToProps = (state) => ({
	pf: state.clienteForm.pf,
	endereco: state.clienteForm.endereco,
	isLoaded: state.clienteForm.isLoaded
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		getCliente,
		change: handleChange('CLIENTE'),
		update: handleUpdate('CLIENTE'),
		clear: clearForm,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ClienteFormEdit);