import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { handleChange, handleSelectChange, clearForm } from '../form/formActions';
import { getParceiroProcedimento } from './parceiroActions';
import Load from '../layout/load';

import { Button, FormText, FormGroup, Label, Input, InputGroup, InputGroupAddon, Row, Col } from 'reactstrap';
import { Popconfirm } from 'antd';

class ParceiroProcedimentoFormEdit extends React.Component {

	constructor(props) {
		super(props);
		
		this.state = {
			isLoaded: false
		};

		this.updateParceiroProcedimento = this.updateParceiroProcedimento.bind(this);
		this.deleteParceiroProcedimento = this.deleteParceiroProcedimento.bind(this);
	}
	
	componentDidMount() {
		this.props.clear('PARCEIRO_PROCEDIMENTO');

        if (this.props.match && this.props.match.params.id) {
            this.props.getParceiroProcedimento(this.props.match.params.id_parceiro, this.props.match.params.id, () => {
                this.setState({isLoaded: true});
            });
		}
	}

	updateParceiroProcedimento(event) {
		event.preventDefault();
		this.setState({isLoaded: false});

		const parceiroProcedimento = {
			observacao: this.props.observacao,
			comissao: this.props.comissao,
			valor_parceiro: this.props.valor_parceiro,
			horario_atendimento: this.props.horario_atendimento
		};

		if (this.props.nome_parceiro_procedimento !== this.props.nome) {
			parceiroProcedimento.nome = this.props.nome_parceiro_procedimento;
		}

		axiosAuth.put(`parceiroprocedimento/${this.props.id_parceiro_procedimento}`, parceiroProcedimento)
			.then(response => {
				this.setState({isLoaded: true});
				if (response.data.status === false) {
					alert(response.message);
				} else {
					this.props.clear('PARCEIRO_PROCEDIMENTO');
					alert('Procedimento atualizado com sucesso!');
					window.location.href = `#/parceiro/v/${this.props.match.params.id_parceiro}/procedimentos`;
				}
			});
	}

	deleteParceiroProcedimento(e) {
		axiosAuth.delete(`parceiroprocedimento/${this.props.id_parceiro_procedimento}`)
			.then(response => {
				this.setState({isLoaded: true});
				if (response.data.status === false) {
					alert(response.message);
				} else {
					this.props.clear('PARCEIRO_PROCEDIMENTO');
					alert('Procedimento removido com sucesso!');
					window.location.href = `#/parceiro/v/${this.props.match.params.id_parceiro}/procedimentos`;
				}
			})
			.catch(err => {
				alert('Ocorreu um erro ao remover o procedimento.');
			});
	}

	render() {
		return ( 
			<form onSubmit={this.updateParceiroProcedimento}>
				{this.state.isLoaded ? '' : <Load />}
				<fieldset>
					<legend>Dados Procedimento</legend>
					<FormGroup>
						<Label for="nome_parceiro_procedimento">Nome</Label>
						<InputGroup>
							<InputGroupAddon addonType="prepend">{this.props.codigo_tuss && `${this.props.codigo_tuss} -`}</InputGroupAddon>
							<Input type="text" name="nome_parceiro_procedimento" id="nome_parceiro_procedimento" required value={this.props.nome_parceiro_procedimento || this.props.nome} onChange={this.props.change} />
						</InputGroup>
					</FormGroup>
					<Row>
						<Col>
							<FormGroup>
								<Label for="valor_parceiro">Valor Parceiro</Label>
								<InputGroup>
									<InputGroupAddon addonType="prepend">R$</InputGroupAddon>
									<Input type="text" name="valor_parceiro" id="valor_parceiro" required value={this.props.valor_parceiro} onChange={this.props.change} />
								</InputGroup>
								<FormText>Valor em reais e com centavos separado por ponto.</FormText>
							</FormGroup>
						</Col>
						<Col>
							<FormGroup>
								<Label for="comissao">Valor da Comissão</Label>
								<InputGroup>
									<InputGroupAddon addonType="prepend">R$</InputGroupAddon>
									<Input type="text" name="comissao" id="comissao" required value={this.props.comissao} onChange={this.props.change} />
								</InputGroup>
								<FormText>Valor em reais e com centavos separado por ponto.</FormText>
							</FormGroup>
						</Col>
					</Row>
					<FormGroup>
						<Label for="horario_atendimento">Horário de Atendimento</Label>
						<Input type="text" name="horario_atendimento" id="horario_atendimento" required value={this.props.horario_atendimento} onChange={this.props.change} />
					</FormGroup>
					<FormGroup>
						<Label for="observacao">Observação</Label>
						<Input type="text" name="observacao" id="observacao" value={this.props.observacao} onChange={this.props.change} />
					</FormGroup>
				</fieldset>
				<Row>
					<Col sm={{ size: 'auto' }}>
						<Button type="submit" outline color="primary">Salvar</Button>
					</Col>
					<Col sm={{ size: 'auto' }}>
						<Popconfirm title="Tem certeza que deseja deletar este procedimento?" onConfirm={this.deleteParceiroProcedimento} okText="Sim" cancelText="Não">
							<Button type="button" outline color="danger">Excluir</Button>
						</Popconfirm>
					</Col>
				</Row>
			</form>
		);
	}
};

const mapStateToProps = (state) => ({
	procedimentosById: state.app.procedimentosById,
	procedimentosOptions: state.app.procedimentosOptions,
	codigo_tuss: state.parceiroProcedimentoForm.codigo_tuss,
	nome: state.parceiroProcedimentoForm.nome,
	nome_parceiro_procedimento: state.parceiroProcedimentoForm.nome_parceiro_procedimento,
	observacao: state.parceiroProcedimentoForm.observacao,
	comissao: state.parceiroProcedimentoForm.comissao,
	valor_parceiro: state.parceiroProcedimentoForm.valor_parceiro,
	horario_atendimento: state.parceiroProcedimentoForm.horario_atendimento,
	id_parceiro_procedimento: state.parceiroProcedimentoForm.id_parceiro_procedimento,
	id_parceiro: state.parceiroProcedimentoForm.id_parceiro
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		getParceiroProcedimento,
		change: handleChange('PARCEIRO_PROCEDIMENTO'),
		changeSelect: handleSelectChange('PARCEIRO_PROCEDIMENTO'),
		clear: clearForm
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ParceiroProcedimentoFormEdit);