import React from 'react';
import { Link } from 'react-router-dom';
import { Tooltip } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import FileSaver from 'file-saver';

const reportUrl = 'relatorio/parceiro';

class RelatorioParceiro extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
            parceiros: [],
			searchOptions: [
				{name: 'Parceiro', value: 'nome'},
			],
			searchFilters: [
				{name: 'Status do Agendamento', value: 'status', type: 'select', options: [{value: 'pendente', label: 'Pendente'}, {value: 'realizado', label: 'Realizado'}, {value: 'pago', label: 'Pago'}]},
				{name: 'Data inicial', value: 'data_inicial', props: {type: 'date'}},
				{name: 'Data final', value: 'data_final', props: {type: 'date'}},
			],
            search: {
                value: '',
                field: '',
                filters: ''
            },
			activeFilter: '',
			isLoaded: false
		};

		this.filter = this.filter.bind(this);
        this.handleDownload = this.handleDownload.bind(this);
	}

	componentDidMount() {	
        axiosAuth.get(reportUrl)
            .then(response => {
                this.setState({ isLoaded: true });
                if (response.data.status !== false) {
                    this.setState({ parceiros: response.data });
                }
            });
	}

    handleDownload() {
        const url = `${reportUrl}${this.state.activeFilter ? this.state.activeFilter + '&' : '?'}export=csv`; 
        axiosAuth.get(url)
            .then((response) => {
                var blob = new Blob([response.data.replaceAll('.', ',')], {type: 'text/csv;charset=utf-8'});
                FileSaver.saveAs(blob, "relatorio.csv");
            });
    }

	filter(value, field, filters) {
		const activeFilter = value ? `/${value}/${field}?${filters}` : `?${filters}`;
		const url = `${reportUrl}${activeFilter}`; 
		this.setState({ isLoaded: false, activeFilter, search: { value, field, filters } });
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.setState({ parceiros: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Parceiro</th>
								<th>Qnt Agendamentos</th>
								<th>Qnt Procedimentos</th>
                                <th><Tooltip title='Valor de agendamentos pagos ao parceiro'>Parceiro Pago</Tooltip></th>
                                <th><Tooltip title='Valor de agendamentos realizados não pagos ao parceiro'>Parceiro Pendente</Tooltip></th>
                                <th><Tooltip title='Valor de agendamentos pendentes de realização no parceiro'>Parceiro Previsto</Tooltip></th>
                                <th><Tooltip title='Somatório dos agendamentos do parceiro'>Parceiro Total</Tooltip></th>
                                <th><Tooltip title='Valor da CF de agendamentos pagos ao parceiro'>CF Pago</Tooltip></th>
                                <th><Tooltip title='Valor da CF de agendamentos realizados não pagos ao parceiro'>CF Pendente</Tooltip></th>
                                <th><Tooltip title='Valor da CF de agendamentos pendentes de realização no parceiro'>CF Previsto</Tooltip></th>
                                <th><Tooltip title='Somatório da CF dos agendamentos do parceiro'>CF Total</Tooltip></th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.parceiros.map(parceiro => (
									<tr>
                                        <td><Link to={`/relatorio/agendamento${this.state.activeFilter ? this.state.activeFilter + '&' : '?'}parceiro=${parceiro.id_parceiro}`}>{parceiro.nome}</Link></td>
                                        <td>{parceiro.qnt_agendamentos}</td>
                                        <td>{parceiro.qnt_procedimentos}</td>
                                        <td>R$&nbsp;{parceiro.valor_parceiro_pago}</td>
                                        <td>R$&nbsp;{parceiro.valor_parceiro_pendente}</td>
                                        <td>R$&nbsp;{parceiro.valor_parceiro_previsto}</td>
                                        <td>R$&nbsp;{parceiro.valor_parceiro_total}</td>
                                        <td>R$&nbsp;{parceiro.valor_cf_pago}</td>
                                        <td>R$&nbsp;{parceiro.valor_cf_pendente}</td>
                                        <td>R$&nbsp;{parceiro.valor_cf_previsto}</td>
                                        <td>R$&nbsp;{parceiro.valor_cf_total}</td>
                                    </tr>
								))
							}
						</tbody>
					</table>
				</div>
                <FloatButton onClick={this.handleDownload} title='Download' icon='download' />
			</div>
		);
	}
}

export default RelatorioParceiro;