import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Modal, ModalHeader, ModalBody } from 'reactstrap';
import { fechaModalAlert } from './modalAlertActions';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class ModalAlert extends React.Component {

	render() {
		return (
			<div>
				<Modal isOpen={this.props.visivel} toggle={this.props.fechaModalAlert} className={`modal-${this.props.tipo}`}>
					<ModalHeader toggle={this.props.fechaModalAlert}><FontAwesomeIcon icon='exclamation-circle' /> {this.props.titulo}</ModalHeader>
					<ModalBody>
						{
							this.props.conteudo ?
								this.props.conteudo.constructor === Object ? 
								(
									<ul>	
										{
											Object.keys(this.props.conteudo).map((key, index) => 
												<li>{this.props.conteudo[key]}</li>
											)
										}
									</ul>
								) : (
									<p>{this.props.conteudo}</p>
								)
							: (
								<p>Ocorreu um erro no servidor ao processar a requisição.</p>
							)
						}
					</ModalBody>
				</Modal>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	visivel: state.modalAlert.visivel,
	tipo: state.modalAlert.tipo,
	titulo: state.modalAlert.titulo,
	conteudo: state.modalAlert.conteudo
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		fechaModalAlert
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ModalAlert);