import React from 'react';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import Load from '../layout/load';

class RepresentanteSinglePagamentos extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            pagamento: {},
            isLoaded: false
        };
    }

    componentDidMount() {
        if (this.props.match && this.props.match.params.id_representante && this.props.match.params.id_representante_pagamento) {
            axiosAuth.get(`representante/${this.props.match.params.id_representante}/pagamento/${this.props.match.params.id_representante_pagamento}`)
                .then(response => {
                    this.setState({isLoaded: true, pagamento: response.data[0]});
                })
                .catch(err => {
                    this.setState({isLoaded: true});
                    alert('Ocorreu um erro ao carregar a página. Por favor, tente novamente!');
                });
        }
    }

    render() {
        const { pagamento } = this.state;

		return (
			<div>
                {this.state.isLoaded || <Load />}
				<dl className="row">
					<dt className="col-sm-3">Código</dt>
					<dd className="col-sm-9">{pagamento.id_representante_pagamento && pagamento.id_representante_pagamento.padStart(10, '0')}</dd>

					<dt className="col-sm-3">Valor</dt>
					<dd className="col-sm-9">R$ {pagamento.total_pagamento}</dd>
					
                    <dt className="col-sm-3">Data criacao</dt>
					<dd className="col-sm-9">{pagamento.data_criacao && pagamento.data_criacao.replace(/^(\d{4})-(\d{2})-(\d{2}) (\d)/, '$3/$2/$1 $4')}</dd>                    
				</dl>
                <table className="table">
					<thead>
						<tr>
							<th>Número</th>
							<th>Data criação</th>
							<th>Valor</th>
						</tr>
					</thead>
					<tbody>
						{
							pagamento.agendamentos && pagamento.agendamentos.map(agendamento => 
								<tr key={`reppagagen${agendamento.id_agendamento}`}>
									<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
									<td>{agendamento.data_criacao.replace(/^(\d{4})-(\d{2})-(\d{2}) (\d)/, '$3/$2/$1 $4')}</td>
									<td>R$ {agendamento.valor_representante || 0}</td>
								</tr>
							)
						}
					</tbody>
				</table>
			</div>
		);
	}
}

export default RepresentanteSinglePagamentos;