import React from 'react';

class EnderecoSingle extends React.Component {

	render() {
		return (
			<div>
				<h3>Endereço</h3>
				<dl className="row">
					<dt className="col-sm-3">Logradouro</dt>
					<dd className="col-sm-9">{this.props.endereco.logradouro}</dd>

					<dt className="col-sm-3">Número</dt>
					<dd className="col-sm-9">{this.props.endereco.numero}</dd>

					<dt className="col-sm-3">Bairro</dt>
					<dd className="col-sm-9">{this.props.endereco.bairro}</dd>

					<dt className="col-sm-3">Complemento</dt>
					<dd className="col-sm-9">{this.props.endereco.complemento}</dd>

					<dt className="col-sm-3">CEP</dt>
					<dd className="col-sm-9">{this.props.endereco.cep && this.props.endereco.cep.replace(/^(\d{5})(\d{3})/, '$1-$2')}</dd>

					<dt className="col-sm-3">Cidade</dt>
					<dd className="col-sm-9">{this.props.endereco.cidade}</dd>

					<dt className="col-sm-3">Estado</dt>
					<dd className="col-sm-9">{this.props.endereco.estado}</dd>
				</dl>
			</div>
		);
	}
}

export default EnderecoSingle;