import React from 'react';
import Page from '../layout/page';

import FinanceiroList from './financeiroList';
import FinanceiroForm from './financeiroForm';
import FinanceiroSingle from './financeiroSingle';

const icon = 'dollar-sign';
const title = 'Financeiro';

export const FinanceiroIndex = (props) => (
	<Page title={title} icon={icon}>
		<FinanceiroList />
	</Page>
);

export const FinanceiroAdd = ({props, match}) => (
	<Page title={title} icon={icon}>
		<FinanceiroForm match={match} />
	</Page>
);

export const FinanceiroView = ({props, match}) => (
	<Page title={title} icon={icon}>
		<FinanceiroSingle match={match} />
	</Page>
);