import axiosAuth from '../../helpers/axiosAuth';

export const getCliente = (cpf, cb=function(){}) => {
	return dispatch => {
		axiosAuth.get(`cliente/cpf/${cpf}`)
			.then(response => {
				if (response.data.status === false) {
					dispatch({type: 'AGENDAMENTO_CLEAR_CLIENTE', payload: response.data.message});
					cb();
				} else {
					dispatch({type: 'AGENDAMENTO_GET_CLIENTE', payload: response.data[0]});
					cb();
				}
			});
	};
}

export const clearCliente = () => {
	return {type: 'AGENDAMENTO_CLEAR_CLIENTE'};
}

export const addProcedimento = () => {
	return {type: 'AGENDAMENTO_ADD_PROCEDIMENTO'};
}

export const removeProcedimento = (id_parceiro_procedimento) => {
	return {type: 'AGENDAMENTO_REMOVE_PROCEDIMENTO', payload: id_parceiro_procedimento};
}

export const filtraParceiroProcedimento = (procedimento) => {
	return dispatch => {
		if (procedimento && procedimento.value) {
			dispatch({type: 'AGENDAMENTO_CHANGE', payload: {'disabledProcedimentos': true}});
			axiosAuth.get(`parceiro/procedimento/${procedimento.value}`)
				.then(response => {
					dispatch({type: 'AGENDAMENTO_CHANGE', payload: {'disabledProcedimentos': false}});
					if (response.data.status === false) {
						dispatch({type: 'AGENDAMENTO_FILTER_PROCEDIMENTO', payload: []});
					} else {	
						const parceirosFiltrados = [];
						response.data.map( p => parceirosFiltrados.push({value: p.id_parceiro_procedimento, label: `R$ ${p.valor_total} - ${p.parceiro_nome}`}) );
						dispatch({type: 'AGENDAMENTO_FILTER_PROCEDIMENTO', payload: parceirosFiltrados});
					}
				});
		} else {
			dispatch({type: 'AGENDAMENTO_FILTER_PROCEDIMENTO', payload: []});
		}
	}
}

export const getParceiroProcedimento = () => {
	return dispatch => { 
		axiosAuth.get(`parceiro/procedimento/0`)
			.then(response => {
				if (response.data.status === false) {
					dispatch({type: 'AGENDAMENTO_CHANGE', payload: {parceiroProcedimentoById: []}});
				} else {
					const parceiroProcedimentoById = response.data.reduce( (procedimentos, p) => {
						procedimentos[p.id_parceiro_procedimento] = {
							id_parceiro_procedimento: p.id_parceiro_procedimento,
							id_parceiro: p.id_parceiro, 
							valor_total: p.valor_total,
							valor_parceiro: p.valor_parceiro,
							codigo_tuss: p.codigo_tuss,
							observacao: p.observacao,
							parceiro: p.parceiro_nome
						}; 
						return procedimentos;
					}, {});
					dispatch({type: 'AGENDAMENTO_CHANGE', payload: {parceiroProcedimentoById: parceiroProcedimentoById}});
				}
			});
	}
}

export const changeDesconto = (event) => {
	return {
		type: 'AGENDAMENTO_CHANGE_DESCONTO',
		payload: {
			index: event.target.name.split('_')[1],
			target: event.target
		}
	};
}