import React from 'react';
import Page from '../layout/page';

import CaixaList from './caixaList';
import CaixaForm from './caixaForm';
import CaixaSingle from './caixaSingle';
import CaixaTransacao from './caixaTransacao';

const icon = 'cash-register';
const title = 'Caixa';

export const CaixaIndex = (props) => (
	<Page title={title} icon={icon}>
		<CaixaList />
	</Page>
);

export const CaixaAdd = (props) => (
	<Page title={title} icon={icon}>
		<CaixaForm />
	</Page>
);

export const CaixaView = ({props, match}) => (
    <Page title={title} icon={icon}>
		<CaixaSingle match={match} />
	</Page>
);

export const CaixaTransacaoAdd = (props) => (
	<Page title={title} icon={icon}>
		<CaixaTransacao />
	</Page>
);