import React from 'react';
import { Modal, ModalHeader, ModalBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import FloatButton from '../layout/floatButton';

class MyModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			modal: this.props.modal || false
		};

		this.toggle = this.toggle.bind(this);
	}

	toggle() {
		this.setState({
			modal: !this.state.modal
		});
	}

	render() {
		return (
			<div>
				{ this.props.hideButton ? '' : <FloatButton icon={this.props.icon} onClick={this.toggle} /> }
				<Modal size={this.props.size || 'lg'} isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
					<ModalHeader toggle={this.toggle}>{this.props.error ? (<FontAwesomeIcon icon='exclamation-circle' />) : ''} {this.props.title}</ModalHeader>
					<ModalBody>{this.props.children}</ModalBody>
				</Modal>
			</div>
		);
	}
}

export default MyModal;