import React from 'react';
import { Link } from 'react-router-dom';
import { Tooltip } from 'antd';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import FileSaver from 'file-saver';

const reportUrl = 'relatorio/financeiro';

class RelatorioFinanceiro extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
            financeiros: [],
			searchOptions: [
				{name: 'Parceiro', value: 'nome'},
			],
			searchFilters: [
				{name: 'Data inicial', value: 'data_inicial', props: {type: 'date'}},
				{name: 'Data final', value: 'data_final', props: {type: 'date'}},
			],
			search: {
                value: '',
                field: '',
                filters: ''
            },
			isLoaded: false
		};

		this.filter = this.filter.bind(this);
		this.handleDownload = this.handleDownload.bind(this);
	}

	componentDidMount() {	
        axiosAuth.get(reportUrl)
            .then(response => {
                this.setState({ isLoaded: true });
                if (response.data.status !== false) {
                    this.setState({ financeiros: response.data });
                }
            });
	}

	handleDownload() {
        const { value, field, filters } = this.state.search;
        const url = value ? `${reportUrl}/${value}/${field}?${filters}&export=csv` : `${reportUrl}?${filters}&export=csv`; 
        axiosAuth.get(url)
			.then((response) => {
            	var blob = new Blob([response.data.replaceAll('.', ',')], {type: 'text/csv;charset=utf-8'});
            	FileSaver.saveAs(blob, "relatorio.csv");
        	});
    }

	filter(value, field, filters) {
		const url = value ? `${reportUrl}/${value}/${field}?${filters}` : `${reportUrl}?${filters}`; 
		this.setState({ isLoaded: false, search: { value, field, filters } });
		axiosAuth.get(url)
			.then(response => {
				this.setState({ isLoaded: true });
				if (response.data.status !== false) {
					this.setState({ financeiros: response.data });
				}
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} filters={this.state.searchFilters} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Parceiro</th>
								<th>
									<Tooltip title='Procedimentos realizados não vinculados a nenhum financeiro'>Pendente de lançamento</Tooltip>
								</th>
								<th>
									<Tooltip title='Procedimentos realizados vinculados a financeiros não pagos'>Pendente de pagamento</Tooltip>
								</th>
							</tr>
						</thead>
						<tbody>
							{
								this.state.financeiros.map(financeiro => (
									<tr>
                                        <td><Link to={`/relatorio/agendamento${this.state.activeFilter ? this.state.activeFilter + '&' : '?'}parceiro=${financeiro.id_parceiro}&status=realizado&tipo=financeiro`}>{financeiro.nome}</Link></td>
                                        <td>R$ {financeiro.procedimentos_pendentes}</td>
                                        <td>R$ {financeiro.financeiros_pendentes}</td>
                                    </tr>
								))
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={this.handleDownload} title='Download' icon='download' />
			</div>
		);
	}
}

export default RelatorioFinanceiro;